// =============================================
// FORM DEFAULT STYLES MENU
//
// 1. LABELS
// 2. SECTION BORDER
// 3. INPUT & TEXTAREA
// 4. RADIO & CHECKBOX
// 5. SELECT
// 6. SELECT DROPDOWN
// 7. MULTI SELECT
// 8. DATE FIELD
// 9. CALENDAR
// 10. BUTTON
// 11. PAGINATION
// =============================================

// =============================================
// 1. LABELS

@include body-class(custom-form, default) {
	
	.forminator-label,
	.forminator-label--limit,
	.forminator-label--helper {
		line-height: $default--label-line-height;
	}

	.forminator-label--validation {
		margin-top: #{$default--padding / 2};
		padding: #{$default--padding / 2} $default--padding;
		border-radius: $default--border-radius;
		line-height: $default--validation-line-height;
	}
}

// =============================================
// 2. SECTION BORDER

@include body-class(custom-form, default) {

	@include section-border();
}

// =============================================
// 3. INPUT & TEXTAREA

@include body-class(custom-form, default) {

	@include input($default--input-map);
}

// =============================================
// 4. RADIO & CHECKBOX

@include body-class(custom-form, default) {

	@include options(radio, $default--option-map);

	@include options(checkbox, $default--option-map);
}

// =============================================
// 5. SELECT

@include body-class(custom-form, default) {

	@include select($default--select-map);
}

// =============================================
// 6. SELECT DROPDOWN

@include dropdown(default, $default--dropdown-map);

// =============================================
// 7. MULTI SELECT

@include body-class(custom-form, default) {

	@include multiselect($default--multiselect-map);
}

// =============================================
// 8. DATE FIELD

@include body-class(custom-form, default) {

	@include date($default--date-map);
}

// =============================================
// 9. CALENDAR

@include calendar(default, $default--calendar-map);

// =============================================
// 10. BUTTON

@include body-class(custom-form, default) {

	@include button($default--button-map);
}

// =============================================
// 11. PAGINATION

@include body-class(custom-form, default) {

	@include pagination($default--pagination-map);
}