// =============================================
// POLL GLOBAL STYLES MENU
//
// 1. BASIC
// 2. NOTIFICATIONS
// 3. POLL CHART
// =============================================

// =============================================
// 1. BASIC

@include body-class(poll) {
	box-sizing: border-box;

	* {
		@extend %text-reset;
		box-sizing: border-box;
	}

	.forminator-poll--image {
		max-width: 100%;
		height: auto;
	}
}

// =============================================
// 2. NOTIFICATIONS

@include body-class(poll) {

	.forminator-label--info {
		@include notice(info);
	}

	.forminator-label--error {
		@include notice(error);
	}

	.forminator-label--notice {
		@include notice(default);
	}

	.forminator-label--success {
		@include notice(success);
	}

	.forminator-label--loading {
		@include notice(loading);
	}
}

// =============================================
// 3. POLL CHART

@include body-class(poll) {

	.forminator-poll--chart {
        max-width: 100%;
        height: auto;
        display: block;
	}
}