$palettes: (
	mono: (
		default:	#000000,
		black:		#000000,
		white:		#FFFFFF,
	),
	gray: (
		default:	#666666,
		light:		#888888,
		dark:		#333333,
	),
	silver: (
		default:	#F2F2F2,
		soft:		#AAAAAA,
		light:		#F8F8F8,
		dark:		#E6E6E6,
	),
	blue: (
		default:	#17A8E3,
		light:		#D4EEF9,
		dark:		#008FCA,
	),
	red: (
		default:	#FF7F83,
		dark:		#CB4B57,
	),
	green: (
		default: #1ABC9C,
	),
	yellow: (
		default:	#F8D247,
		light:		#FFF4D2,
	)
) !default;

// =============================================
// COLORS MENU
//
// 1. MAIN COLORS
// 2. NOTICES COLORS
// 3. MATERIAL COLORS
// =============================================

// =============================================
// 1. MAIN COLORS

$white:			palette(mono, white)		!default;
$black:			palette(mono, black)		!default;

$silver:		palette(silver, default)	!default;
$silver-light:	palette(silver, light)		!default;
$silver-dark:	palette(silver, dark)		!default;

$gray:			palette(gray, default)		!default;
$gray-soft:		palette(gray, soft)			!default;
$gray-light:	palette(gray, light)		!default;
$gray-dark:		palette(gray, dark)			!default;

$blue:			palette(blue, default)		!default;
$blue-light:	palette(blue, light)		!default;
$blue-dark:		palette(blue, dark)			!default;

$red:			palette(red, default)		!default;
$red-dark:		palette(red, dark)			!default;

$green:			palette(green, default)		!default;

$yellow:		palette(yellow, default)	!default;
$yellow-light:	palette(yellow, light)		!default;

// =============================================
// 3. NOTICES COLORS

$notice--default-background:		$yellow-light	!default;
$notice--default-color:				$gray-dark		!default;
$notice--default-icon-color:		$yellow			!default;

$notice--info-background:			$silver-light	!default;
$notice--info-color:				$gray-light		!default;
$notice--info-icon-color:			$blue			!default;

$notice--error-background:			$red-dark		!default;
$notice--error-color:				$white			!default;
$notice--error-icon-color:			$white			!default;

$notice--success-background:		$green			!default;
$notice--success-color:				$white			!default;
$notice--success-icon-color:		$white			!default;

$notice--loading-background:		$blue-light		!default;
$notice--loading-color:				$gray-light		!default;
$notice--loading-icon-color:		$white			!default;

// =============================================
// 3. MATERIAL COLORS

$material--shadow-color:	rgba(0,0,0,0.3)		!default;
$material--image-mask:		rgba(0,0,0,0.6)		!default;
$material--card-border:		rgba(0,0,0,0.1)		!default;