// =============================================
// There are two way for using this mixin. You
// can display module class only or module class
// with module theme. For both cases follow this:
//
// A) To get quiz module class only:
//    @include body-class(quiz, '') { ... }
//
// B) To get quiz module + flat theme class:
//    @include body-class(quiz, flat) { ... }
// =============================================

@mixin body-class($module-type, $module-theme: null) {

	@if ($module-theme == null) {

		.forminator-#{$module-type} {
			@content;
		}
	} @else {

		.forminator-#{$module-type}.forminator-design--#{$module-theme} {
			margin: $gutter-sm auto;
			padding: 0;
			border: 0;
			box-sizing: border-box;

			&:first-child {
				margin-top: 0;

				@include media(min-width, sm) {
					margin-top: 0;
				}
			}

			&:last-child {
				margin-bottom: 0;

				@include media(min-width, sm) {
					margin-bottom: 0;
				}
			}

			@include media(min-width, sm) {
				margin: $gutter auto;
			}

			* {
				@extend %text-reset;
				box-sizing: border-box;
			}

			@if $module-type == quiz {

				.forminator-quiz--title,
				.forminator-quiz--description,
				.forminator-quiz--image {
					margin: 0 0 $gutter-sm;
					
					&:last-child {
						margin-bottom: 0;
					}
				}
				
				.forminator-quiz--title,
				.forminator-quiz--description {
					padding: 0;
					border: 0;
					letter-spacing: 0;
					text-transform: none;
					text-decoration: none;
				}

				.forminator-quiz--title {
					line-height: $quiz--title-line-height;
				}

				.forminator-quiz--description {
					line-height: $quiz--description-line-height;
				}

				.forminator-quiz--image {
					width: 100%;
					display: block;
					margin: 0;
					padding: 0;
					border: 0;

					img {
						max-width: 100%;
						display: block;
						margin: 0 auto;
						border: 0;
						padding: 0;
					}
				}

				.forminator-quiz--summary {
					margin: $gutter-sm 0;

					h1, h2, h3, h4, h5, h6, p {
						padding: 0;
						line-height: 1.2em;
		
						&:first-child {
							margin-top: 0;
						}
		
						&:last-child {
							margin-bottom: 0;
						}
					}

					h1, h2, h3, h4, h5, h6 {
						margin-top: 0.8em;
						margin-bottom: 0.8em;
					}

					p {
						margin-top: 0.5em;
						margin-bottom: 0.5em;
					}

					&:first-child {
						margin-top: 0;
					}

					&:last-child {
						margin-bottom: 0;
					}
				}

				.forminator-social--icons {
					margin: #{$gutter-sm / 2} -#{$gutter-sm / 2};
					list-style: none;

					li {
						display: inline-block;
						margin: 0;
						padding: 0 #{$gutter-sm / 2};
						border: 0;
						list-style: none;
					}

					&:first-child {
						margin-top: 0;
					}

					&:last-child {
						margin-bottom: 0;
					}
				}

				.forminator-social--icon {
					line-height: 1;

					a {
						display: block;
						margin: 0;
						padding: 0;

						&,
						&:hover,
						&:active,
						&:visited {
							border: 0;
							outline: none;
							box-shadow: none;
							text-decoration: none;
						}

						&,
						&:visited {
							@include opacity(1);
						}

						&:hover,
						&:active {
							@include opacity(0.7);
						}

						&:before {
							display: block;
						}
					}
				}

				&:not(.forminator-size--small) {

					.forminator-quiz--title,
					.forminator-quiz--description,
					.forminator-quiz--image {
						
						&:last-child {
				
							@include media(min-width, sm) {
								margin-bottom: 0;
							}
						}

						@include media(min-width, sm) {
							margin: 0 0 $gutter;
						}
					}

					.forminator-quiz--summary {

						&:first-child {

							@include media(min-width, sm) {
								margin-top: 0;
							}
						}

						&:last-child {

							@include media(min-width, sm) {
								margin-bottom: 0;
							}
						}

						@include media(min-width, sm) {
							margin: $gutter 0;
						}
					}

					.forminator-social--icons {

						li {

							@include media(min-width, sm) {
								padding: 0 #{$gutter / 2};
							}
						}

						&:first-child {

							@include media(min-width, sm) {
								margin-top: 0;
							}
						}

						&:last-child {

							@include media(min-width, sm) {
								margin-bottom: 0;
							}
						}

						@include media(min-width, sm) {
							margin: #{$gutter / 2} -#{$gutter / 2};
						}
					}

					&.forminator-design--grid {

						.forminator-quiz--title,
						.forminator-quiz--description {
	
							@include media(min-width, sm) {
								text-align: center;
							}
						}
	
						.forminator-quiz--image img {
	
							@include media(min-width, sm) {
								margin: 0 auto;
							}
						}
					}
				}
			}

			@content;
		}
	}
}