@include body-class('custom-form', '') {

	.forminator-hidden {
		display: none !important;
	}

	.forminator-hidden-mobile {
		
		@include media(max-width, sm) {
			display: none !important;
		}
	}

	.forminator-hidden-desktop {

		@include media(min-width, sm) {
			display: none !important;
		}
	}
}