// =============================================
// 1. LABELS

@include body-class(custom-form, bold) {
	
	.forminator-label,
	.forminator-label--limit,
	.forminator-label--helper {
		line-height: $bold--line-height;
	}

	.forminator-label--validation {
		margin-top: #{$bold--padding / 2};
		padding: #{$bold--padding / 2} $bold--padding;
		line-height: $bold--line-height;
	}
}

// =============================================
// 2. SECTION BORDER

@include body-class(custom-form, bold) {

	@include section-border();
}

// =============================================
// 3. INPUT & TEXTAREA

@include body-class(custom-form, bold) {

	@include input($bold--input-map);
}

// =============================================
// 4. RADIO & CHECKBOX

@include body-class(custom-form, bold) {

	@include options(radio, $bold--option-map);

	@include options(checkbox, $bold--option-map);
}

// =============================================
// 5. SELECT

@include body-class(custom-form, bold) {

	@include select($bold--select-map);
}

// =============================================
// 6. SELECT DROPDOWN

@include dropdown(bold, $bold--dropdown-map);

// =============================================
// 7. MULTI SELECT

@include body-class(custom-form, bold) {
	
	@include multiselect($bold--multiselect-map);
}

// =============================================
// 8. DATE FIELD

@include body-class(custom-form, bold) {

	@include date($bold--date-map);
}

// =============================================
// 9. CALENDAR

@include calendar(bold, $bold--calendar-map);

// =============================================
// 10. BUTTON

@include body-class(custom-form, bold) {

	@include button($bold--button-map);
}

// =============================================
// 11. PAGINATION

@include body-class(custom-form, bold) {

	@include pagination($bold--pagination-map);
}