// =============================================
// FORM FLAT STYLES MENU
//
// 1. LABELS
// 2. SECTION BORDER
// 3. INPUT & TEXTAREA
// 4. RADIO & CHECKBOX
// 5. SELECT
// 6. SELECT DROPDOWN
// 7. MULTI SELECT
// 8. DATE FIELD
// 9. CALENDAR
// 10. BUTTON
// 11. PAGINATION
// =============================================

// =============================================
// 1. LABELS

@include body-class(custom-form, flat) {
	
	.forminator-label,
	.forminator-label--limit,
	.forminator-label--helper {
		line-height: $flat--line-height;
	}

	.forminator-label--validation {
		margin-top: #{$flat--padding / 2};
		padding: #{$flat--padding / 2} $flat--padding;
		line-height: $flat--line-height;
	}
}

// =============================================
// 2. SECTION BORDER

@include body-class(custom-form, flat) {

	@include section-border();
}

// =============================================
// 3. INPUT & TEXTAREA

@include body-class(custom-form, flat) {

	@include input($flat--input-map);
}

// =============================================
// 4. RADIO & CHECKBOX

@include body-class(custom-form, flat) {

	@include options(radio, $flat--option-map);

	@include options(checkbox, $flat--option-map);
}

// =============================================
// 5. SELECT

@include body-class(custom-form, flat) {

	@include select($flat--select-map);
}

// =============================================
// 6. SELECT DROPDOWN

@include dropdown(flat, $flat--dropdown-map);

// =============================================
// 7. MULTI SELECT

@include body-class(custom-form, flat) {
	
	@include multiselect($flat--multiselect-map);
}

// =============================================
// 8. DATE FIELD

@include body-class(custom-form, flat) {

	@include date($flat--date-map);
}

// =============================================
// 9. CALENDAR

@include calendar(flat, $flat--calendar-map);

// =============================================
// 10. BUTTON

@include body-class(custom-form, flat) {

	@include button($flat--button-map);
}

// =============================================
// 11. PAGINATION

@include body-class(custom-form, flat) {

	@include pagination($flat--pagination-map, flat);
}