@include body-class(custom-form) {

	.forminator-row {
		display: block;
		margin-bottom: $gutter-sm;

		.forminator-col {
			margin-bottom: $gutter-sm;

			&:last-child {
				margin-bottom: 0;
			}
		}

		&:last-child {
			margin-bottom: 0;
		}
	}

	.forminator-row--time {

		+ .forminator-label--validation {
			display: none;
		}
	}

	&.forminator-enclosed {

		.forminator-row {
			margin-bottom: $gutter-enclosed-sm;

			.forminator-col {
				margin-bottom: $gutter-enclosed-sm;

				&:last-child {
					margin-bottom: 0;
				}
			}

			&:last-child {
				margin-bottom: 0;
			}
		}
	}

	&:not(.forminator-size--small) {

		.forminator-row {

			.forminator-col {

				@include media(min-width, sm) {
					flex: 0 0 auto;
					margin-bottom: 0;
					padding-right: #{$gutter / 2};
					padding-left: #{$gutter / 2};
				}
			}

			.forminator-col-3 {

				@include media(min-width, sm) {
					max-width: 25%;
					flex-basis: 25%;
				}
			}

			.forminator-col-4 {

				@include media(min-width, sm) {
					max-width: 33.33%;
					flex-basis: 33.33%;
				}
			}

			.forminator-col-6 {

				@include media(min-width, sm) {
					max-width: 50%;
					flex-basis: 50%;
				}
			}

			.forminator-col-12 {

				@include media(min-width, sm) {
					min-width: 100%;
					max-width: 100%;
					flex-basis: 100%;
				}
			}

			.forminator-col-3,
			.forminator-col-4 {

				.forminator-row--inner {
					
					.forminator-col {

						&:last-child {

							@include media(min-width, sm) {
								margin-bottom: 0;
							}
						}

						@include media(min-width, sm) {
							margin-bottom: $gutter;
							padding-right: 0;
							padding-left: 0;
						}
					}

					.forminator-col-3,
					.forminator-col-4,
					.forminator-col-6 {

						@include media(min-width, sm) {
							max-width: 100%;
						}
					}

					@include media(min-width, sm) {
						display: block;
						margin-right: 0;
						margin-left: 0;
					}
				}
			}

			&:last-child {

				@include media(min-width, sm) {
					margin-bottom: 0;
				}
			}

			@include media(min-width, sm) {
				display: flex;
				margin-right: -#{$gutter / 2};
				margin-bottom: $gutter;
				margin-left: -#{$gutter / 2};
			}
		}

		.forminator-row--time {

			.forminator-field {

				.forminator-label--validation {

					@include media(min-width, sm) {
						display: none;
					}
				}
			}

			+ .forminator-label--validation {

				@include media(min-width, sm) {
					display: block;
				}
			}

			@include media(min-width, sm) {
                align-items: flex-end;
            }
		}

		&.forminator-enclosed {

			.forminator-row {

				.forminator-col {

					@include media(min-width, sm) {
						padding-right: #{$gutter-enclosed / 2};
						padding-left: #{$gutter-enclosed / 2};
					}
				}

				.forminator-col-3,
				.forminator-col-4 {

					.forminator-row--inner {

						.forminator-col {

							&:last-child {

								@include media(min-width, sm) {
									margin-bottom: 0;
								}
							}

							@include media(min-width, sm) {
								margin-bottom: $gutter-enclosed;
							}
						}
					}
				}

				&:last-child {

					@include media(min-width, sm) {
						margin-bottom: 0;
					}
				}

				@include media(min-width, sm) {
					margin-right: -#{$gutter-enclosed / 2};
					margin-bottom: $gutter-enclosed;
					margin-left: -#{$gutter-enclosed / 2};
				}
			}
		}
	}
}