@mixin options($options-type: checkbox, $options-map: unset, $options-theme: null) {

	@if ($options-type == checkbox) or ($options-type == radio) {

		.forminator-#{$options-type} {
			display: flex;
			position: relative;
			margin: 5px 0;

			input {
				@extend %screen-reader-only;

				&:checked + .forminator-#{$options-type}--design {

					&:before {
						@include opacity(1);
					}
				}

				@if $options-theme == material {

					&:focus + .forminator-#{$options-type}--design:after {
						@include opacity(1);
						animation-name: click-animation;
					}
				}
			}

			.forminator-#{$options-type}--design {
				cursor: pointer;
				display: flex;
				flex-direction: column;
				align-items: center;
				justify-content: center;
				margin: 0;
				padding: 0;

				&:before {
					@include opacity(0);
					display: block;
				}

				+ .forminator-#{$options-type}--label {
					margin-left: 10px;
				}

				@if $options-theme == material {
					position: relative;

					&:after {
						content: " ";
						@include opacity(0);
						position: absolute;
						border-radius: 50%;
						transform: scale(0);
						transform-origin: center;
						transform-style: preserve-3d;
						animation-duration: 0.6s;
						animation-fill-mode: both;

						@if $options-map != null {

							$has-size: map-has-key($options-map, size);
							$size: map-get($options-map, size);

							@if $has-size {
								width: $size;
								height: $size;
							}
						}
					}
				}
			}

			.forminator-#{$options-type}--label {
				cursor: default;
				display: block;
				margin: 0;
				padding: 0;
				border: 0;
				letter-spacing: 0;

				&[for] {
					cursor: pointer;
				}
			}

			@content;
		}

		@if $options-map != null {

			$has-size: map-has-key($options-map, size);
			$has-icon-size: map-has-key($options-map, icon-size);
			$has-border-width: map-has-key($options-map, border-width);
			$has-border-style: map-has-key($options-map, border-style);
			$has-border-radius: map-has-key($options-map, border-radius);
			$has-line-height: map-has-key($options-map, line-height);
			$has-transition: map-has-key($options-map, transition);

			$size: map-get($options-map, size);
			$icon-size: map-get($options-map, icon-size);
			$border-width: map-get($options-map, border-width);
			$border-style: map-get($options-map, border-style);
			$border-radius: map-get($options-map, border-radius);
			$line-height: map-get($options-map, line-height);
			$transition: map-get($options-map, transition);

			.forminator-#{$options-type} .forminator-#{$options-type}--design {

				@if $has-size {
					width: $size;
					height: $size;
					flex: 0 0 $size;
				}

				@if $has-border-width and $has-border-style {
					border-width: $border-width;
					border-style: $border-style;
				}
	
				@else {
					border: 0;
				}

				@if $has-border-radius {
					border-radius: $border-radius;
				}

				@if $has-transition {
					transition: $transition;
				}
			}

			@if $has-line-height {
				
				.forminator-#{$options-type} .forminator-#{$options-type}--label {
					line-height: $line-height;
				}
			}

			@if $has-icon-size {

				@if $options-type == checkbox {
					
					.forminator-#{$options-type} .forminator-#{$options-type}--design:before {
						font-size: $icon-size;
					}
				}

				@if $options-type == radio {
					
					.forminator-#{$options-type} .forminator-#{$options-type}--design:before {
						width: $icon-size;
						height: $icon-size;
					}
				}
			}
		}

		@if ($options-type == radio) {

			.forminator-#{$options-type} {
	
				.forminator-#{$options-type}--design {
					border-radius: 100%;

					&:before {
						content: " ";
						border-radius: 100%;
					}
				}
			}
		}
	}

	@else {

		@warn "Unfortunately, no type could be retrieved from `#{$options-type}`. "
		+ "Use either `radio` or `checkbox`.";
	}
}