@mixin poll-answers() {

	.forminator-poll--answers {
		margin: 0;

		&, li {
			padding: 0;
			border: 0;
			list-style: none;
		}

		li {
			margin: #{$gutter-sm / 2} 0;

			&:first-child {
				margin-top: 0;
			}

			&:last-child {
				margin-bottom: 0;
			}
		}
	}
}