// =============================================
// 1. POLL QUESTION

@include body-class(poll, material) {

	@include poll-question($material--poll-question);
}

// =============================================
// 2. POLL DESCRIPTION

@include body-class(poll, material) {

	@include poll-description($material--poll-description);
}

// =============================================
// 3. POLL ANSWERS

@include body-class(poll, material) {

	@include poll-answers();
}

// =============================================
// 4. POLL ANSWER

@include body-class(poll, material) {

	@include options(radio, $material--option-map, material);
}

// =============================================
// 5. CUSTOM ANSWER

@include body-class(poll, material) {

	@include input($material--input-map, material) {

		.forminator-input--wrap {
			position: relative;

			&:after {
                content: " ";
                height: 2px;
                display: block;
                position: absolute;
                right: 50%;
                bottom: 0;
                left: 50%;
				transition: $material--transition-alt;
            }
		}

		.forminator-floating--input {
			position: relative;
			z-index: 1;

			.forminator-label {
				pointer-events: none;
				transform-origin: 0;
				transition: transform $material--transition-alt, $material--transition;
			}
		}

		.forminator-floating--input {
			
			.forminator-label {
				transform: translateY(#{$material--input-padding + $material--input-line-height});
			}
		}

		.forminator-floating--input {

			+ .forminator-input,
			+ .forminator-input--wrap .forminator-input {

				&:-moz-placeholder {
                    @include opacity(0);
                }
                
                &::-moz-placeholder {
                    @include opacity(0);
                }
				
                &:-ms-input-placeholder {
                    @include opacity(0);
                }
				
                &::-webkit-input-placeholder {
                    @include opacity(0);
                }
			}
		}

		.forminator-is_hover,
		.forminator-is_active,
		.forminator-has_error {

			.forminator-input--wrap {

				&:after {
					right: 0;
					left: 0;
				}
			}
		}

		.forminator-is_active,
		.forminator-is_filled {

			.forminator-floating--input {
				
				.forminator-label {
					transform: translateY(0);
                }
			}
		}

		.forminator-is_active {

			.forminator-floating--input {

				+ .forminator-input,
				+ .forminator-input--wrap .forminator-input {

					&:-moz-placeholder {
						@include opacity(1);
					}
					
					&::-moz-placeholder {
						@include opacity(1);
					}
					
					&:-ms-input-placeholder {
						@include opacity(1);
					}
					
					&::-webkit-input-placeholder {
						@include opacity(1);
					}
				}
			}
		}
	}
}

// =============================================
// 6. POLL ACTIONS

@include body-class(poll, material) {

	@include poll-actions($material--poll-actions);
}

// =============================================
// 7. BUTTON

@include body-class(poll, material) {

	@include button($material--button-map, material);
}