@mixin poll-description($description-map: null) {

	.forminator-poll--description {
		margin: 0 0 #{$gutter-sm / 2};
		padding: 0;
		border: 0;

		@if $description-map != null {

			$has-font-size: map-has-key($description-map, font-size);
			$has-line-height: map-has-key($description-map, line-height);
			$has-font-weight: map-has-key($description-map, font-weight);
	
			$font-size: map-get($description-map, font-size);
			$line-height: map-get($description-map, line-height);
			$font-weight: map-get($description-map, font-weight);
	
			@if $has-font-size {
				font-size: $font-size;
			}

			@if $has-line-height {
				line-height: $line-height;
			}

			@if $has-font-weight {
				font-weight: $font-weight;
			}
		}
	}

	&:not(.forminator-size--small) {

		.forminator-poll--description {

			@include media(min-width, sm) {
				margin: 0 0 #{$gutter / 2};
			}
		}
	}
}