@mixin date($date-map: null, $date-theme: default) {

	.forminator-date {
		position: relative;

		&.forminator-has_icon {

			@if $date-theme == material {
				display: flex;
				align-items: center;
				@include icon(before, calendar) {
					flex: 0 0 auto;
					margin-right: #{$gutter-sm / 2};
				}

				.forminator-input,
				.forminator-input--wrap {
					flex: 1;
				}
			}

			@else {

				@include icon(after, calendar) {
					position: absolute;
				}
			}

			@if $date-map != null {

				$has-padding: map-has-key($date-map, padding);
				$has-icon-size: map-has-key($date-map, icon-size);
				$has-icon-height: map-has-key($date-map, icon-height);

				$padding: map-get($date-map, padding);
				$icon-size: map-get($date-map, icon-size);
				$icon-height: map-get($date-map, icon-height);

				@if $date-theme == material {

					&:before {
	
						@if $has-icon-size {
							font-size: $icon-size;
						}

						@if $has-icon-height {
							min-width: $icon-height;
							flex: 0 0 $icon-height;
						}
					}
				}

				@else {

					@if $has-icon-height and $has-padding {

						.forminator-input {
							padding-right: #{$icon-height + ($padding * 2)};
						}
					}

					&:after {

						@if $has-padding {
							top: $padding;
							right: $padding;
						}

						@else {
							top: 0;
							right: 0;
						}

						@if $has-icon-size {
							font-size: $icon-size;
						}

						@if $has-icon-height {
							width: $icon-height;
							line-height: $icon-height;
							text-align: center;
						}
					}
				}
			}
		}
	}

	@if $date-theme == material {

		&:not(.forminator-size--small) {

			.forminator-date {

				&.forminator-has_icon:before {
					margin-right: #{$gutter / 2};
				}
			}
		}
	}
}