@mixin pagination($pagination-map: null, $pagination-theme: default) {

	.forminator-pagination--bar {
		width: 100%;
		display: flex;
		align-items: center;
		margin-bottom: $gutter-sm;

		.forminator-bar--text {
			flex: 0 0 auto;
			margin: 0 #{$gutter-sm / 2};

			@if $pagination-map != null {
				
				$has-bar-size: map-has-key($pagination-map, bar-size);
				$bar-size: map-get($pagination-map, bar-size);

				@if $has-bar-size {
					line-height: $bar-size;
				}
			}

			&:first-child {
				margin-left: 0;
			}

			&:last-child {
				margin-right: 0;
			}
		}

		.forminator-bar--progress {
			overflow: hidden;
			flex: 1;
			position: relative;
			margin: 0;

			@if $pagination-map != null {
				
				$has-bar-size: map-has-key($pagination-map, bar-size);
				$has-border-width: map-has-key($pagination-map, border-width);
				$has-border-style: map-has-key($pagination-map, border-style);

				$bar-size: map-get($pagination-map, bar-size);
				$border-width: map-get($pagination-map, border-width);
				$border-style: map-get($pagination-map, border-style);

				@if $has-bar-size {
					height: $bar-size;

					@if $pagination-theme != material {

						&, span {
							border-radius: #{$bar-size / 2};
						}
					}
				}

				@if ($pagination-theme == bold) and ($pagination-theme == default) {

					@if $has-border-width and $has-border-style {
						border-width: $border-width;
						border-style: $border-style;
					}
				}
			}

			span {
				display: block;
				position: absolute;
				top: 0;
				bottom: 0;
				left: 0;
			}
		}
	}

	.forminator-pagination--nav {
		margin: 0 0 $gutter-sm;

		&, li {
			display: block;
			position: relative;
			padding: 0;
			border: 0;
			list-style: none;
		}

		&:before {
			content: " ";
			height: auto;
			position: absolute;

			@if $pagination-map != null {
					
				$has-dot-size: map-has-key($pagination-map, dot-size);
				$has-border-width: map-has-key($pagination-map, border-width);
				$has-line-height: map-has-key($pagination-map, line-height);

				$dot-size: map-get($pagination-map, dot-size);
				$border-width: map-get($pagination-map, border-width);
				$line-height: map-get($pagination-map, line-height);

				@if $has-border-width {
					width: $border-width;
				}

				@if $has-dot-size and $has-border-width {
					top: #{($dot-size - $border-width) / 2};
					bottom: #{($dot-size - $border-width) / 2};
					left: #{($dot-size - $border-width) / 2};
				}

				@if $has-dot-size and not $has-border-width {
					top: $dot-size;
					bottom: $dot-size;
					left: $dot-size;
				}
			}
		}

		li {
			margin: #{$gutter-sm / 2} 0;

			&:first-child {
				margin-top: 0;
			}

			&:last-child {
				margin-bottom: 0;
			}
		}
	}

	.forminator-pagination--footer {
		margin-top: $gutter-sm;

		@if $pagination-map != null {
			
			$has-border-width: map-has-key($pagination-map, border-width);
			$has-border-style: map-has-key($pagination-map, border-style);

			$border-width: map-get($pagination-map, border-width);
			$border-style: map-get($pagination-map, border-style);

			@if ($pagination-theme == bold or $pagination-theme == default ) and ($has-border-width and $has-border-style) {
				padding-top: $gutter-sm;
				border-top-width: $border-width;
				border-top-style: $border-style;
			}
		}
	}

	&:not(.forminator-size--small) {

		.forminator-pagination--bar {

			.forminator-bar--text {

				&:first-child {
					
					@include media(min-width, sm) {
						margin-left: 0;
					}
				}
	
				&:last-child {
					
					@include media(min-width, sm) {
						margin-right: 0;
					}
				}

				@include media(min-width, sm) {
					margin: 0 #{$gutter / 2};
				}
			}
			
			@include media(min-width, sm) {
				margin-bottom: $gutter;
			}
		}

		.forminator-pagination--nav {

			&:before {

				@include media(min-width, sm) {
					content: unset;
				}
			}

			@include media(min-width, sm) {
				margin: 0 0 $gutter;
			}
		}

		.forminator-pagination--footer {
			
			@include media(min-width, sm) {
				display: flex;
				justify-content: space-between;
				margin-top: $gutter;

				@if $pagination-map != null {
			
					$has-border-width: map-has-key($pagination-map, border-width);
					$has-border-style: map-has-key($pagination-map, border-style);
		
					$border-width: map-get($pagination-map, border-width);
					$border-style: map-get($pagination-map, border-style);
		
					@if ($pagination-theme == bold or $pagination-theme == default ) and ($has-border-width and $has-border-style) {
						padding-top: $gutter;
					}
				}
			}
		}
	}

	@if $pagination-theme != material {

		.forminator-pagination--nav {
	
			li {
	
				&:before {
					right: 50%;
					left: 0;
				}
	
				&:after {
					right: 0;
					left: 50%;
				}

				.forminator-step-text  {
					display: block;
	
					@if $pagination-map != null {
	
						$has-dot-size: map-has-key($pagination-map, dot-size);
						$has-line-height: map-has-key($pagination-map, line-height);
	
						$dot-size: map-get($pagination-map, dot-size);
						$line-height: map-get($pagination-map, line-height);
	
						@if $has-dot-size {
							padding-left: #{map-get($pagination-map, dot-size) + ($gutter-sm / 2)};
						}
	
						@if $has-line-height {
							line-height: $line-height;
						}
					}
				}

				.forminator-step-dot {
					display: block;
					position: absolute;
					left: 0;

					@if $pagination-map != null {

						$has-dot-size: map-has-key($pagination-map, dot-size);
						$has-border-width: map-has-key($pagination-map, border-width);
						$has-border-style: map-has-key($pagination-map, border-style);
						$has-line-height: map-has-key($pagination-map, line-height);
						$has-transition: map-has-key($pagination-map, transition);

						$dot-size: map-get($pagination-map, dot-size);
						$border-width: map-get($pagination-map, border-width);
						$border-style: map-get($pagination-map, border-style);
						$line-height: map-get($pagination-map, line-height);
						$transition: map-get($pagination-map, transition);

						@if $has-dot-size {
							width: $dot-size;
							height: $dot-size;
							border-radius: 100%;
						}

						@if $pagination-theme != flat and ($has-border-width and $has-border-style) {
							border-width: $border-width;
							border-style: $border-style;
						}

						@else {
							border: 0;
						}

						@if $has-dot-size and $has-line-height {

							@if $line-height > $dot-size {
								top: #{($line-height - $dot-size) / 2};
							}

							@else if $line-height < $dot-size {
								top: -#{($dot-size - $line-height) / 2};
							}

							@else {
								top: 0;
							}
						}

						@if $has-transition {
							transition: $transition;
						}
					}

					@if $pagination-theme == flat {
						transform: scale(0.7);
					}
				}

				@if $pagination-theme == flat {

					&.forminator-step-current {

						.forminator-step-dot {
							transform: scale(1);
						}
					}
				}
			}
		}

		&:not(.forminator-size--small) {

			.forminator-pagination--nav {

				li {

					@include media(min-width, sm) {
						min-width: 33.33%;
						flex: 1 0 33.33%;
						margin: 0;
						text-align: center;
					}
				}

				li:before,
				li:after {

					@include media(min-width, sm) {
						content: " ";
						width: auto;
						display: block;
						position: absolute;

						@if $pagination-map != null {

							$has-dot-size: map-has-key($pagination-map, dot-size);
							$has-border-width: map-has-key($pagination-map, border-width);

							$dot-size: map-get($pagination-map, dot-size);
							$border-width: map-get($pagination-map, border-width);

							@if $has-border-width {
								height: $border-width;
							}

							@if $has-dot-size and $has-border-width {
								bottom: #{($dot-size - $border-width) / 2};
							}

							@if $has-dot-size and not $has-border-width {
								bottom: #{$dot-size / 2};
							}
						}
					}
				}

				li .forminator-step-dot {

					@include media(min-width, sm) {
						position: relative;
						z-index: 1;
						top: auto;
						left: auto;
						margin: 5px auto 0;
					}
				}

				li .forminator-step-text {

					@include media(min-width, sm) {
						padding-left: 0;
					}
				}

				@include media(min-width, sm) {
					display: flex;
					flex-wrap: wrap;
					align-items: flex-end;
				}
			}
		}
	}

	@else {

		.forminator-pagination--nav {
			position: relative;
			counter-reset: pagination-steps;

			li {
				display: flex;

				@include icon(before, check) {
					counter-increment: pagination-steps;
					margin-right: #{$gutter-sm / 2};
					border-radius: 50%;
					text-align: center;

					@if $pagination-map != null {

						$has-dot-size: map-has-key($pagination-map, dot-size);
						$dot-size: map-get($pagination-map, dot-size);

						@if $has-dot-size {
							width: $dot-size;
							height: $dot-size;
							font-size: #{$dot-size / 2};
							line-height: $dot-size;
						}
					}
				}

				.forminator-step-text {
					align-self: center;
				}

				&.forminator-step-current {

					&:before {
						content: counter(pagination-steps);
						font-family: inherit !important;
					}

					~ li:before {
						content: counter(pagination-steps);
						font-family: inherit !important;
					}
				}
			}
		}

		&:not(.forminator-size--small) {

			.forminator-pagination--nav {

				li {

					@include media(min-width, sm) {
						flex: 0 0 auto;
						margin: 0;
						padding: 0 #{$gutter / 2};
					}
				}

				.forminator-nav-border:not(:last-child) {

					@include media(min-width, sm) {
						flex: 1;

						@if $pagination-map != null {

							$has-border-width: map-has-key($pagination-map, border-width);
							$border-width: map-get($pagination-map, border-width);
	
							@if $has-border-width {
								height: $border-width;
							}
						}
					}
				}

				@include media(min-width, sm) {
					display: flex;
					flex-wrap: wrap;
					align-items: center;
					margin: 0 -#{$gutter / 2} $gutter;
				}
			}
		}
	}

	// REMOVE LATER WHEN SETTINGS FOR THIS
	// ARE ADDED TO APPEARANCE SETTINGS.
	.forminator-pagination--bar {
		
		.forminator-bar--progress {
			background-color: #EDEDED;

			span {
				background-color: palette(blue, default);
			}
		}
	}
}