@mixin select($select-map: unset, $select-theme: null) {

	.forminator-time + .select2,
	.forminator-select + .select2 {
		width: 100% !important;

		* {
			display: block;
		}

		.forminator-select2 {
			height: auto;

			&.select2-selection--single {

				@if $select-theme == material {
					background-color: transparent;
				}

				.select2-selection__rendered {
					padding-left: 0;
				}
			}
		}
	}

	@if $select-map != null {

		$has-icon: map-has-key($select-map, icon);
		$has-icon-size: map-has-key($select-map, icon-size);
		$has-padding: map-has-key($select-map, padding);
		$has-border-width: map-has-key($select-map, border-width);
		$has-border-style: map-has-key($select-map, border-style);
		$has-border-radius: map-has-key($select-map, border-radius);
		$has-line-height: map-has-key($select-map, line-height);
		$has-transition: map-has-key($select-map, transition);

		$icon: map-get($select-map, icon);
		$icon-size: map-get($select-map, icon-size);
		$padding: map-get($select-map, padding);
		$border-width: map-get($select-map, border-width);
		$border-style: map-get($select-map, border-style);
		$border-radius: map-get($select-map, border-radius);
		$line-height: map-get($select-map, line-height);
		$transition: map-get($select-map, transition);

		.forminator-time + .select2,
		.forminator-select + .select2 {

			.forminator-select2 {

				@if $select-theme == material {

					&:after {
                        content: " ";
                        display: block;
                        position: absolute;
                        right: 50%;
						left: 50%;
						
						@if $has-border-width {
							height: #{$border-width * 2};
							bottom: -#{$border-width};
						}
						
						@if $has-transition {
							transition: $transition;
						}
                    }

					@if $has-padding and $has-border-width {
						padding: $padding 0 #{$padding - $border-width};
					}
	
					@else if $has-padding and not $has-border-width {
						padding: $padding 0;
					}

					@else {
						padding: 0;
					}

					@if $has-border-width and $has-border-style {
						border: 0;
						border-bottom-width: $border-width;
						border-bottom-style: $border-style;
					}

					@else {
						border: 0;
					}
				}

				@else {

					@if $has-padding and $has-border-width {
						padding: #{$padding - $border-width};
					}
	
					@else if $has-padding and not $has-border-width {
						padding: $padding;
					}

					@else {
						padding: 0;
					}

					@if $has-border-width and $has-border-style {
						border-width: $border-width;
						border-style: $border-style;
					}
	
					@else {
						border: 0;
					}
				}

				@if $has-border-radius {
					border-radius: $border-radius;
				}

				@else {
					border-radius: 0;
				}

				@if $has-transition {
					transition: $transition;
				}

				@if $has-line-height or $has-transition {

					&.select2-selection--single {
					
						.select2-selection__rendered {
							
							@if $has-line-height {
								line-height: $line-height;
							}

							@if $has-transition {
								transition: $transition;
							}
						}
					}
				}
			}
		}

		@if $select-theme == material {

			.forminator-time + .select2,
			.forminator-select + .select2 {

				&.select2-container--open {
				
					.forminator-select2:after {
						right: 0;
						left: 0;
					}
				}
			}

			.forminator-is_hover {

				.forminator-time + .select2,
				.forminator-select + .select2 {

					.forminator-select2:after {
						right: 0;
						left: 0;
					}
				}
			}
		}

		@if $has-icon {

			.forminator-time + .select2,
			.forminator-select + .select2 {

				.forminator-select2.select2-selection--single {
					position: relative;

					.select2-selection__rendered {

						@if $select-theme == material {

							@if $has-padding {
								padding-right: #{$icon + $padding};
							}
						}
	
						@else {
	
							@if $has-padding and $has-border-width {
								padding-right: #{$icon + ($padding * 2) - $border-width};
							}
	
							@if $has-padding and not $has-border-width {
								padding-right: #{$icon + ($padding * 2)};
							}
						}
					}

					.select2-selection__arrow {
						width: $icon;
						height: $icon;
						display: flex;
						flex-direction: column;
						align-items: center;
						justify-content: center;
						position: absolute;

						@if $select-theme == material {
							right: 0;

							@if $has-padding {
								top: $padding;
							}
						}

						@else {

							@if $has-padding and $has-border-width {
								top: #{$padding - $border-width};
								right: #{$padding - $border-width};
							}
	
							@if $has-padding and not $has-border-width {
								top: $padding;
								right: $padding;
							}
						}

						@if $has-transition {

							@include icon(before, chevron-down) {
								transition: transform 0.2s linear, $transition;

								@if $has-icon-size {
									font-size: $icon-size;
								}
							}
						}

						@else {

							@include icon(before, chevron-down) {
								transition: transform 0.2s linear;
							}
						}

						b {
							display: none;
						}
					}
				}

				&.select2-container--open {

					.forminator-select2.select2-selection--single {

						.select2-selection__arrow:before {
							transform: rotate(180deg);
						}
					}
				}
			}
		}
	}
}