@mixin dropdown($dropdown-theme: default, $dropdown-map: null) {

	@if $dropdown-theme != null {

		body > .select2-container {

			.forminator-droptime--#{$dropdown-theme},
			.forminator-dropdown--#{$dropdown-theme} {
				display: block;
				position: relative;
				box-sizing: border-box;
	
				* {
					box-sizing: border-box;
					@extend %text-reset;
				}

				.select2-search,
				.select2-search--dropdown {
					
					&,
					&:hover,
					&:focus,
					&:active {
						outline: none;
						box-shadow: none;
					}
				}
	
				.select2-results {
	
					.select2-results__option {
						margin: 0;
						padding: 0;
						border: 0;
						list-style: none;

						.select2-results__option {
							margin: 0;
							list-style: none;
						}
					}
				}

				&.select2-dropdown--above {
					bottom: 2px;
				}

				&.select2-dropdown--below {
					margin-top: 2px;
				}

				@if $dropdown-map != null {

					$has-padding: map-has-key($dropdown-map, padding);
					$has-border-width: map-has-key($dropdown-map, border-width);
					$has-border-style: map-has-key($dropdown-map, border-style);
					$has-border-radius: map-has-key($dropdown-map, border-radius);
					$has-line-height: map-has-key($dropdown-map, line-height);
					$has-transition: map-has-key($dropdown-map, transition);
	
					$padding: map-get($dropdown-map, padding);
					$border-width: map-get($dropdown-map, border-width);
					$border-style: map-get($dropdown-map, border-style);
					$border-radius: map-get($dropdown-map, border-radius);
					$line-height: map-get($dropdown-map, line-height);
					$transition: map-get($dropdown-map, transition);
					
					@if $has-border-width and $has-border-style {
						border-width: $border-width;
						border-style: $border-style;
					}

					@else {
						border: 0;
					}

					@if $has-border-radius {
						border-radius: $border-radius;
						overflow: hidden;
					}

					@else {
						border-radius: 0;
					}

					.select2-search,
					.select2-search--dropdown {

						@if $has-padding and $has-border-width {
							padding: #{$padding - $border-width};
						}

						@if $has-padding and not $has-border-width {
							padding: $padding;
						}

						.select2-search__field {

							@if $has-padding and $has-border-width {
								padding: #{$padding - $border-width};
							}

							@if $has-padding and not $has-border-width {
								padding: $padding;
							}

							@if $has-border-width and $has-border-style {
								border-width: $border-width;
								border-style: $border-style;
							}

							@else {
								border: 0;
							}

							@if $has-border-radius {
								border-radius: $border-radius;
							}
		
							@else {
								border-radius: 0;
							}

							@if $has-line-height {
								line-height: $line-height;
							}
						}
					}

					.select2-results {

						.select2-results__option {

							@if $has-padding and $has-border-width {
								padding: #{$padding - $border-width};
							}

							@if $has-padding and not $has-border-width {
								padding: $padding;
							}

							@if $has-line-height {
								line-height: $line-height;
							}

							@if $has-transition {
								transition: $transition;
							}
						}
					}
				}

				@if $dropdown-theme == material {

					&:after {
						content: " ";
						position: absolute;
						z-index: -1;
						top: 0;
						right: 0;
						bottom: 0;
						left: 0;
						box-shadow: $material--card-shadow-hover;
					}
				}
			}
	
			.forminator-droptime--#{$dropdown-theme} {
				
				.select2-search,
				.select2-search--dropdown {
					display: none;
				}
			}
		}
	}

	@else {

		@warn "Unfortunately, no type could be retrieved from `#{$dropdown-theme}`. "
		+ "Use either `bold`, `flat`, `default` or `material`.";
	}
}