// =============================================
// 1. BASIC EXTENSIONS
//    ******************************************
//    1.1. Basic Reset

%basic-reset {
	margin: 0;
	padding: 0;
	border: 0;
}

//    ******************************************
//    1.2. Text Reset

%text-reset {
	font-variant-ligatures: none;
	-webkit-font-variant-ligatures: none;
	text-rendering: optimizeLegibility;
	-moz-osx-font-smoothing: grayscale;
	font-smoothing: antialiased;
	-webkit-font-smoothing: antialiased;
	text-shadow: rgba(0, 0, 0, .01) 0 0 1px;
}

//    ******************************************
//    1.3. Truncate Text

%truncate-text {
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}

//    ******************************************
//    1.4. Screen Reader (Only)

%screen-reader-only {
	width: 1px;
	height: 1px;
	overflow: hidden;
	position: absolute !important;
	margin: -1px;
	border: 0;
	padding: 0;
	clip: rect(1px, 1px, 1px, 1px);
	clip-path: inset(50%);
	word-wrap: normal !important;
}

// =============================================
// 2. LABEL

%fui-label {
	cursor: default;
	display: block;
	@extend %basic-reset;
	letter-spacing: $letter-spacing;

	&[for] {
		cursor: pointer;
	}

	.wpdui-icon-asterisk {
		display: inline-flex;
		vertical-align: super;
		font-size: 6px;

		&:before {
			display: block;
		}
	}
}