@font-face {
    font-family: "wpdui-icon";
    src: url("../fonts/wpdui-icon.eot?n9b1n0");
    src: url("../fonts/wpdui-icon.eot?n9b1n0#iefix") format("embedded-opentype"),
         url("../fonts/wpdui-icon.woff2?n9b1n0") format("woff2"),
         url("../fonts/wpdui-icon.ttf?n9b1n0") format("truetype"),
         url("../fonts/wpdui-icon.woff?n9b1n0") format("woff"),
         url("../fonts/wpdui-icon.svg?n9b1n0") format("svg");
    font-weight: normal;
    font-style: normal;
}

@each $name, $char in $icons {

	[class*="wpdui-icon-"] {
		display: inline-block;
		@include icon(both);
	}
	
	.wpdui-icon-#{$name}:before {
		content: $char;
	}
}