@mixin quiz-answer($answer-map: null, $answer-theme: null) {

	.forminator-answer {
		margin: #{$gutter-sm / 2} 0;
		pointer-events: none;

		&, span {
			display: block;
		}

		input {
			@extend %screen-reader-only;

			&:checked + span {
				pointer-events: none;

				@if $answer-map != null {

					$has-box-shadow: map-has-key($answer-map, box-shadow);
					$box-shadow: map-get($answer-map, box-shadow);

					@if $has-box-shadow {
						box-shadow: 0 3px 8px $box-shadow;
					}
				}

				.forminator-answer--check,
				.forminator-answer--status {

					&:after {
						@include opacity(1);
					}

					&:before {
						animation-name: click-animation;
					}
				}

				@if $answer-theme == material {

					.forminator-answer--image:before {
						@include opacity(1);
					}
				}
			}

			&:disabled + span,
			&.forminator-has-been-disabled + span {
				pointer-events: none;
			}
		}

		.forminator-answer--design {
			width: 100%;
			cursor: pointer;
			pointer-events: initial;
			display: flex;
			position: relative;

			@if $answer-map != null {

				$has-padding: map-has-key($answer-map, padding);
				$has-border-width: map-has-key($answer-map, border-width);
				$has-border-style: map-has-key($answer-map, border-style);
				$has-border-radius: map-has-key($answer-map, border-radius);
				$has-box-shadow: map-has-key($answer-map, box-shadow);
				$has-transition: map-has-key($answer-map, transition);

				$padding: map-get($answer-map, padding);
				$border-width: map-get($answer-map, border-width);
				$border-style: map-get($answer-map, border-style);
				$border-radius: map-get($answer-map, border-radius);
				$box-shadow: map-get($answer-map, box-shadow);
				$transition: map-get($answer-map, transition);

				@if $answer-theme != material {

					@if $has-padding and $has-border-width {
						padding: #{$padding - $border-width};
					}
	
					@else if $has-padding and not $has-border-width {
						padding: $padding;
					}
	
					@if $has-border-width and $has-border-style {
						border-width: $border-width;
						border-style: $border-style;
					}
				}

				@if $has-border-radius {
					border-radius: $border-radius;
				}

				@if $has-box-shadow {
					box-shadow: 0 1px 1px $box-shadow;

					&:hover {
						box-shadow: 0 3px 8px $box-shadow;
					}
				}

				@if $has-transition {
					transition: $transition;
				}
			}

			@if $answer-theme == material {
				overflow: hidden;
			}
		}

		.forminator-answer--image {
			width: $quiz--image-sm;
			height: $quiz--image-sm;
			background-color: transparent;
			background-repeat: no-repeat;
			background-size: cover;
			background-position: center;

			@if $answer-theme == material {
				position: relative;

				&:before {
					content: " ";
					@include opacity(0);
					position: absolute;
					top: 0;
					right: 0;
					bottom: 0;
					left: 0;
					background-color: $material--image-mask;

					@if $answer-map != null {

						@if map-has-key($answer-map, transition) {
							transition: map-get($answer-map, transition);
						}
					}
				}

				+ .forminator-answer--check,
				+ .forminator-answer--status {
					position: absolute;
					top: #{($quiz--image-sm - $quiz--checkbox-sm) / 2};
					left: #{($quiz--image-sm - $quiz--checkbox-sm) / 2};
				}
			}

			@else {

				+ .forminator-answer--check,
				+ .forminator-answer--status {
					margin-top: #{($quiz--image-sm - $quiz--checkbox-sm) / 2};

					@if $answer-map != null {

						$has-padding: map-has-key($answer-map, padding);
						$padding: map-get($answer-map, padding);
	
						@if $has-padding {
							margin-left: $padding;
						}
					}
				}
			}
		}

		.forminator-answer--check,
		.forminator-answer--status {
			width: $quiz--checkbox-sm;
			height: $quiz--checkbox-sm;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;

			@if $answer-map != null {

				$has-padding: map-has-key($answer-map, padding);
				$has-border-width: map-has-key($answer-map, border-width);
				$has-border-style: map-has-key($answer-map, border-style);
				$has-border-radius: map-has-key($answer-map, border-radius);
				$has-transition: map-has-key($answer-map, transition);

				$padding: map-get($answer-map, padding);
				$border-width: map-get($answer-map, border-width);
				$border-style: map-get($answer-map, border-style);
				$border-radius: map-get($answer-map, border-radius);
				$transition: map-get($answer-map, transition);

				@if $has-border-width and $has-border-style {
					border-width: $border-width;
					border-style: $border-style;
				}

				@if $has-border-radius {
					border-radius: $border-radius;
				}

				@if $has-transition {
					transition: $transition;
				}

				@if $has-padding {

					+ .forminator-answer--name {
						margin-left: $padding;
					}
				}
			}

			@if $answer-theme == material {

				&:before {
					content: " ";
					width: $quiz--checkbox-sm;
					height: $quiz--checkbox-sm;
					position: absolute;
					border-radius: 50%;
					transform: scale(0);
					transform-origin: center;
					transform-style: preserve-3d;
					animation-duration: 1s;
					animation-fill-mode: both;
				}
			}
		}

		.forminator-answer--check {

			@include icon(after, "check", true) {
				@include opacity(0);
				display: block;
				font-size: $quiz--icon-sm;
			}
		}

		.forminator-answer--status {

			@include icon(after, false, true) {
				@include opacity(0);
				display: block;
				font-size: $quiz--icon-sm;
			}
		}

		.forminator-answer--name {
			flex: 1;
			align-self: center;
		}

		&:first-child {
			margin-top: 0;
		}

		&:last-child {
			margin-bottom: 0;
		}

		&.forminator-is_correct {

			.forminator-answer--status:after {
				content: "\28";
			}
		}

		&.forminator-is_incorrect {

			.forminator-answer--status:after {
				content: "\29";
			}
		}

		&.forminator-empty,
		&.forminator-only--image {

			@if $answer-map != null {

				$has-padding: map-has-key($answer-map, padding);
				$has-border-width: map-has-key($answer-map, border-width);
				$has-border-style: map-has-key($answer-map, border-style);

				$padding: map-get($answer-map, padding);
				$border-width: map-get($answer-map, border-width);
				$border-style: map-get($answer-map, border-style);

				@if $answer-theme != material {

					@if $has-padding {
						max-width: #{$quiz--image-sm + ($padding * 2)};
					}
	
					@else if $has-border-width and not $has-padding {
						max-width: #{$quiz--image-sm + ($border-width * 2)};
					}
	
					@else {
						max-width: $quiz--image-sm;
					}
				}

				@else {
					max-width: $quiz--image-sm;
				}
			}

			@else {
				max-width: $quiz--image-sm;
			}
		}

		&.forminator-empty {

			.forminator-answer--design {
				width: 100%;
				display: flex;
				flex-direction: column;
				align-items: center;
				justify-content: center;

				@if $answer-map != null {

					$has-padding: map-has-key($answer-map, padding);
					$has-border-width: map-has-key($answer-map, border-width);
					$has-border-style: map-has-key($answer-map, border-style);
	
					$padding: map-get($answer-map, padding);
					$border-width: map-get($answer-map, border-width);
					$border-style: map-get($answer-map, border-style);

					@if $answer-theme != material {

						@if $has-padding {
							height: #{$quiz--image-sm + ($padding * 2)};
						}
		
						@else if $has-border-width and not $has-padding {
							height: #{$quiz--image-sm + ($border-width * 2)};
						}

						@else {
							height: $quiz--image-sm;
						}
					}

					@else {
						height: $quiz--image-sm;
					}
				}

				@else {
					height: $quiz--image-sm;
				}
			}
		}

		@if $answer-theme != material {

			&:not(.forminator-only--text):not(.forminator-empty) {

				.forminator-answer--check {
					margin-top: #{($quiz--image-sm - $quiz--checkbox-sm) / 2};
				}
			}

			&.forminator-only--image {

				.forminator-answer--check,
				.forminator-answer--status {
					display: none;
				}
			}
		}

		@else {

			@if $answer-map != null {

				@if map-has-key($answer-map, padding) {

					&.forminator-only--text {

						.forminator-answer--design {
							padding: map-get($answer-map, padding);
						}
					}

					&:not(.forminator-only--text) {
						
						.forminator-answer--name {
							padding: map-get($answer-map, padding) map-get($answer-map, padding) map-get($answer-map, padding) 0;
						}
					}
				}

				@else {

					&:not(.forminator-only--text) {
						
						.forminator-answer--name {
							padding: $gutter-sm $gutter-sm $gutter-sm 0;
						}
					}
				}
			}
		}
	}

	&:not(.forminator-size--small) {

		.forminator-answer {

			&:first-child {

				@include media(min-width, sm) {
					margin-top: 0;
				}
			}

			&:last-child {

				@include media(min-width, sm) {
					margin-bottom: 0;
				}
			}

			@include media(min-width, sm) {
				margin: #{$gutter / 2} 0;
			}
		}

		&.forminator-design--grid {

			.forminator-answer {

				.forminator-answer--design {

					@include media(min-width, sm) {
						display: block;
						height: 100%;
					}
				}

				.forminator-answer--image {

					+ .forminator-answer--check,
					+ .forminator-answer--status {

						@include media(min-width, sm) {
							margin-left: 0;

							@if $answer-map != null {

								$has-padding: map-has-key($answer-map, padding);
								$has-border-width: map-has-key($answer-map, border-width);
		
								$padding: map-get($answer-map, padding);
								$border-width: map-get($answer-map, border-width);

								@if $answer-theme != material {

									@if $has-padding and $has-border-width {
										top: #{$quiz--image + ($padding * 2) - $border-width};
									}
			
									@else if $has-padding and not $has-border-width {
										top: #{$quiz--image + ($padding * 2)};
									}
		
									@else {
										top: #{$quiz--image + ($gutter / 2)};
									}
								}

								@else {
									left: auto;

									@if $has-padding {
										top: $padding;
										right: $padding;
									}

									@else {
										top: #{$gutter / 2};
										right: #{$gutter / 2};
									}
								}
							}
	
							@else {
								
								@if $answer-theme != material {
									top: #{$quiz--image + ($gutter / 2)};
								}

								@else {
									top: #{$gutter / 2};
								}
							}
						}
					}

					@include media(min-width, sm) {
						width: 100%;
						height: $quiz--image;
					}
				}

				.forminator-answer--check,
				.forminator-answer--status {

					&:after {

						@include media(min-width, sm) {
							font-size: $quiz--icon;
						}
					}

					+ .forminator-answer--name {

						@include media(min-width, sm) {

							@if $answer-map != null {

								$has-padding: map-has-key($answer-map, padding);
								$padding: map-get($answer-map, padding);

								@if $answer-theme != material {

									@if $has-padding {
										margin-left: #{$quiz--checkbox + $padding};
									}
		
									@else {
										margin-left: #{$quiz--checkbox + ($gutter / 2)};
									}
								}

								@else {
									
									@if $has-padding {
										margin-left: $padding;
									}

									@else {
										margin-left: #{$gutter / 2};
									}
								}
							}
	
							@else {
								margin-left: #{$quiz--checkbox + ($gutter / 2)};
							}
						}
					}

					@include media(min-width, sm) {
						width: $quiz--checkbox;
						height: $quiz--checkbox;

						@if $answer-theme != material {
							position: absolute;
							margin-top: 0;
						}
					}
				}

				.forminator-answer--name {

					@include media(min-width, sm) {
						flex: 1;
						align-self: unset;
					}
				}

				&:not(.forminator-only--text) {

					.forminator-answer--check,
					.forminator-answer--status {

						+ .forminator-answer--name {

							@include media(min-width, sm) {

								@if $answer-map != null {

									$has-padding: map-has-key($answer-map, padding);
									$padding: map-get($answer-map, padding);
		
									@if $has-padding {
										margin-top: $padding;
									}
		
									@else {
										margin-top: #{$gutter / 2};
									}
								}
		
								@else {
									margin-top: #{$gutter / 2};
								}

								@if $answer-theme == material {
									padding-top: 0;
								}
							}
						}
					}
				}

				&.forminator-empty {

					.forminator-answer--design {

						@include media(min-width, sm) {
							display: flex;
							height: $quiz--image;
						}
					}

					.forminator-answer--image {

						+ .forminator-answer--check {
	
							@include media(min-width, sm) {
	
								@if $answer-map != null {
	
									$has-padding: map-has-key($answer-map, padding);
									$has-border-width: map-has-key($answer-map, border-width);
			
									$padding: map-get($answer-map, padding);
									$border-width: map-get($answer-map, border-width);
			
									@if $has-padding and $has-border-width {
										top: #{$quiz--image + ($padding * 2) - $border-width};
										left: #{$padding - $border-width};
									}
			
									@else if $has-padding and not $has-border-width {
										top: #{$quiz--image + ($padding * 2)};
										left: $padding;
									}
		
									@else {
										top: #{$quiz--image + ($gutter / 2)};
										left: #{$gutter / 2};
									}
								}
		
								@else {
									top: #{$quiz--image + ($gutter / 2)};
									left: #{$gutter / 2};
								}
							}
						}
					}
				}

				&.forminator-only--text {

					.forminator-answer--design {
						
						@include media(min-width, sm) {
							display: flex;
						}
					}
				}

				@include media(min-width, sm) {
					max-width: 33.33%;
					flex: 0 0 33.33%;
					margin: #{$gutter / 2} 0;
					padding: 0 #{$gutter / 2};
				}
			}
		}
	}

	@content;
}