// =============================================
// FORM GLOBAL STYLES MENU
//
// 1. BASIC
// 2. NOTIFICATIONS
// 3. FIELD'S LABEL
// 4. SECTION FIELD
// =============================================

// =============================================
// 1. BASIC

@include body-class(custom-form) {
	box-sizing: border-box;

	* {
		@extend %text-reset;
		box-sizing: border-box;
	}

	.forminator-hidden {
		display: none !important;
	}
}

// =============================================
// 2. NOTIFICATIONS

@include body-class(custom-form) {

	.forminator-label--info {
		@include notice(info);
	}

	.forminator-label--error {
		@include notice(error);
	}

	.forminator-label--notice {
		@include notice(default);
	}

	.forminator-label--success {
		@include notice(success);
	}

	.forminator-label--loading {
		@include notice(loading);
	}

	.forminator-cform-response-message {

		[class^='forminator-label--'] {
			margin-bottom: $gutter-sm;
		}
	}

	&.forminator-enclosed {

		.forminator-cform-response-message {

			[class^='forminator-label--'] {
				margin-bottom: $gutter-enclosed-sm;
			}
		}
	}

	&:not(.forminator-size--small) {

		.forminator-cform-response-message {

			[class^='forminator-label--'] {
	
				@include media(min-width, sm) {
					margin-bottom: $gutter;
				}
			}
		}

		&.forminator-enclosed {

			.forminator-cform-response-message {
	
				[class^='forminator-label--'] {
	
					@include media(min-width, sm) {
						margin-bottom: $gutter-enclosed;
					}
				}
			}
		}
	}
}

// =============================================
// 3. FIELD'S LABEL

@include body-class(custom-form) {

	.forminator-label,
	.forminator-label--limit,
	.forminator-label--helper,
	.forminator-label--validation {
		@extend %fui-label;
	}

	.forminator-label--limit,
	.forminator-label--helper {
		cursor: text;
	}

	.forminator-field--helper {
		display: flex;
		margin: 5px 0 0;

		.forminator-label--helper {
			flex: 1;

			+ .forminator-label--limit {
				margin-left: #{$gutter-sm / 2};
			}
		}

		.forminator-label--limit {
			flex: 0 0 auto;
			text-align: right;

			&:only-child {
				flex: 1;
			}
		}
	}

	&.forminator-enclosed {

		.forminator-field--helper {
			margin: 0;

			.forminator-label--helper {

				+ .forminator-label--limit {
					margin-left: #{$gutter-enclosed-sm / 2};
				}
			}
		}
	}

	&:not(.forminator-size--small) {

		.forminator-field--helper {

			.forminator-label--helper {

				+ .forminator-label--limit {

					@include media(min-width, sm) {
						margin-left: #{$gutter / 2};
					}
				}
			}
		}
	}
}

// =============================================
// 4. SECTION FIELD

@include body-class(custom-form) {

	.forminator-break {

		.forminator-title,
		.forminator-subtitle {
			@extend %basic-reset;

			+ .forminator-border {
				margin-top: $gutter-sm;
			}
		}

		.forminator-title {
			line-height: 1.34em;

			+ .forminator-subtitle {
				margin-top: 0.56em;
			}
		}

		.forminator-subtitle {
			line-height: 1.2em;
		}
	}

	&.forminator-enclosed {

		.forminator-break {

			.forminator-title,
			.forminator-subtitle {

				+ .forminator-border {
					margin-top: $gutter-enclosed-sm;
				}
			}
		}
	}

	&:not(.forminator-size--small) {

		.forminator-break {

			.forminator-title,
			.forminator-subtitle {

				+ .forminator-border {
					
					@include media(min-width, sm) {
						margin-top: $gutter;
					}
				}
			}
		}

		&.forminator-enclosed {

			.forminator-break {

				.forinator-title,
				.forminator-subtitle {

					+ .forminator-border {

						@include media(min-width, sm) {
							margin-top: $gutter-enclosed;
						}
					}
				}
			}
		}
	}
}

// =============================================
// 5. WYSIWYG

@include body-class(custom-form) {

	@include wysiwyg();
}