@mixin calendar($calendar-theme: default, $calendar-map: null) {

	.forminator-calendar--#{$calendar-theme} {
		padding: 2px 0;

		* {
			@extend %text-reset;
		}

		.ui-datepicker-header,
		.ui-datepicker-calendar {
			margin: 0;
		}

		.ui-datepicker-header {
			display: block;
			position: relative;

			.ui-datepicker-prev,
			.ui-datepicker-next {
				cursor: pointer;
				display: block;
				position: absolute;
				text-align: center;

				&:before {
					display: block;
				}

				span {
					display: none;
				}
			}

			.ui-datepicker-prev {
				@include icon(before, chevron-left);
			}

			.ui-datepicker-next {
				@include icon(before, chevron-right);
			}

			.ui-datepicker-title {
				display: flex;

				select {
					display: block;
					flex: 1;
					margin: 0;
				}
			}
		}

		.ui-datepicker-calendar {
			padding: 0;
			border-collapse: unset;
			border-spacing: unset;

			thead tr,
			tbody tr {
				border: 0;
			}

			thead tr th,
			tbody tr td {
				border: 0;
				text-align: center;

				a, span {
					display: block;
					@extend %basic-reset;
				}
			}

			thead tr th {
				font-weight: bold;
			}

			tbody tr td {

				&.ui-state-disabled,
				&.ui-datepicker-unselectable {
					pointer-events: none;

					a, span {
						@include opacity(0.4);
					}
				}
			}
		}

		@if $calendar-map != null {

			$has-padding: map-has-key($calendar-map, padding);
			$has-padding-head: map-has-key($calendar-map, padding-head);
			$has-border-width: map-has-key($calendar-map, border-width);
			$has-border-style: map-has-key($calendar-map, border-style);
			$has-border-radius: map-has-key($calendar-map, border-radius);
			$has-cell-size: map-has-key($calendar-map, cell-size);
			$has-icon: map-has-key($calendar-map, icon);
			$has-icon-size: map-has-key($calendar-map, icon-size);
			$has-shadow: map-has-key($calendar-map, shadow);
			$has-transition: map-has-key($calendar-map, transition);

			$padding: map-get($calendar-map, padding);
			$padding-head: map-get($calendar-map, padding-head);
			$border-width: map-get($calendar-map, border-width);
			$border-style: map-get($calendar-map, border-style);
			$border-radius: map-get($calendar-map, border-radius);
			$cell-size: map-get($calendar-map, cell-size);
			$icon: map-get($calendar-map, icon);
			$icon-size: map-get($calendar-map, icon-size);
			$shadow: map-get($calendar-map, shadow);
			$transition: map-get($calendar-map, transition);

			@if $has-shadow {
				
				&:before {
					content: " ";
					position: absolute;
					top: 0;
					right: 0;
					bottom: 0;
					left: 0;
					box-shadow: $shadow;
				}
			}

			.ui-datepicker-header,
			.ui-datepicker-calendar {

				@if $has-shadow {
					position: relative;
					z-index: 1;
				}

				@if $has-border-width {
					border-width: $border-width;
				}

				@else {
					border-width: 0;
				}

				@if $has-border-style {
					border-style: $border-style;
				}
			}

			.ui-datepicker-header {

				@if $calendar-theme == material {

					@if $has-padding-head {
						padding-top: $padding-head;
						padding-bottom: $padding-head;

						.ui-datepicker-prev,
						.ui-datepicker-next {
							top: $padding-head;
						}
					}
				}

				@else {

					@if $has-padding {
						padding-top: $padding;
						padding-bottom: $padding;
	
						.ui-datepicker-prev,
						.ui-datepicker-next {
							top: $padding;
						}
					}
				}

				@if $has-padding {

					.ui-datepicker-prev {
						left: $padding;
					}

					.ui-datepicker-next {
						right: $padding;
					}
				}

				@if $has-border-width {
					border-bottom: 0;
				}

				@if $has-border-radius {
					border-radius: $border-radius $border-radius 0 0;
				}

				@if $has-icon {

					.ui-datepicker-prev,
					.ui-datepicker-next {
						width: $icon;
						height: $icon;
						
						&:before {
							line-height: $icon;
						}
					}
				}

				@if $has-icon-size {

					.ui-datepicker-prev,
					.ui-datepicker-next {
						
						&:before {
							font-size: $icon-size;
						}
					}
				}

				.ui-datepicker-title {
					
					@if $has-padding {
						margin: 0 $padding;

						select + select {
							margin-left: $padding;
						}
					}

					@else {
						margin: 0;
					}

					@if $has-icon and $has-padding {
						padding: 0 #{$icon + $padding};
					}

					@if $has-icon and not $has-padding {
						padding: 0 $icon;
					}

					@if $has-icon {

						select {
							height: $icon;
						}
					}
				}
			}

			.ui-datepicker-calendar {

				@if $has-border-width {
					border-top: 0;
				}

				@if $has-border-radius {
					border-radius: 0 0 $border-radius $border-radius;
				}

				thead tr th,
				tbody tr td {
					
					@if $has-padding {
						padding: $padding;
					}

					@if $has-cell-size and $has-padding {
						width: $cell-size;
						height: $cell-size;
						line-height: #{$cell-size - ($padding * 2)};

						a, span {
							width: #{$cell-size - ($padding * 2)};
							height: #{$cell-size - ($padding * 2)};
						}
					}

					@if $has-cell-size and not $has-padding {
						width: $cell-size;
						height: $cell-size;
						line-height: $cell-size;

						a, span {
							width: $cell-size;
							height: $cell-size;
						}
					}

					@if $calendar-theme == material {

						a, span {
							border-radius: 50%;
						}
					}

					@else {

						@if $has-border-radius {

							a, span {
								border-radius: $border-radius;
							}
						}
					}

					@if $has-transition {

						a {
							transition: $transition;
						}
					}
				}
			}
		}
	}
}