@mixin input($input-map: null, $input-theme: default) {

	.forminator-input,
	.forminator-textarea {
		width: 100%;
		max-width: 100%;
		display: block;
		margin: 0;
		padding: 0;
		border: 0;
		background-image: unset;

		@if $input-theme == material {
			background-color: transparent;
		}

		&,
		&:hover,
		&:focus,
		&:active,
		&:disabled {
			outline: none;
			box-shadow: none;
		}

		&,
		&:hover,
		&:focus,
		&:active {
			@include opacity(1);
		}

		&:disabled {
			pointer-events: none;
			border-style: dotted;

			&:-moz-placeholder {
				@include opacity(0.4);
			}
		
			&::-moz-placeholder {
				@include opacity(0.4);
			}

			&:-ms-input-placeholder {
				@include opacity(0.4);
			}

			&::-webkit-input-placeholder {
				@include opacity(0.4);
			}
		}
	}

	.forminator-textarea {
		height: auto;
		min-height: 140px;
		resize: vertical;
	}

	.intl-tel-input {
		display: block;
	}

	@if $input-map != null {

		$has-padding: map-has-key($input-map, padding);
		$has-border-width: map-has-key($input-map, border-width);
		$has-border-style: map-has-key($input-map, border-style);
		$has-border-radius: map-has-key($input-map, border-radius);
		$has-line-height: map-has-key($input-map, line-height);
		$has-transition: map-has-key($input-map, transition);

		$padding: map-get($input-map, padding);
		$border-width: map-get($input-map, border-width);
		$border-style: map-get($input-map, border-style);
		$border-radius: map-get($input-map, border-radius);
		$line-height: map-get($input-map, line-height);
		$transition: map-get($input-map, transition);

		.forminator-input,
		.forminator-textarea {

			@if $has-line-height {
				line-height: $line-height;
			}

			@if $has-transition {
				transition: $transition;
			}
		}

		.forminator-input {

			@if $input-theme == material {
				border-radius: 0;

				@if $has-padding {
					padding-top: $padding;
				}
	
				@if $has-padding and $has-border-width {
					padding-bottom: #{$padding - $border-width};
				}
	
				@if $has-padding and not $has-border-width {
					padding-bottom: $padding;
				}
	
				@if $has-border-width and $has-border-style {
					border-top: 0;
					border-right: 0;
					border-left: 0;
					border-bottom-width: $border-width;
					border-bottom-style: $border-style;
				}
	
				@else {
					border: 0;
				}
			}

			@else {

				@if $has-padding and $has-border-width {
					padding: #{$padding - $border-width};
				}

				@if $has-padding and not $has-border-width {
					padding: $padding;
				}

				@if $has-border-width and $has-border-style {
					border-width: $border-width;
					border-style: $border-style;
				}

				@else {
					border: 0;
				}

				@if $has-border-radius {
					border-radius: $border-radius;
				}
	
				@else {
					border-radius: 0;
				}
			}
		}

		.forminator-textarea {

			@if $has-padding and $has-border-width {
				padding: #{$padding - $border-width};
			}

			@if $has-padding and not $has-border-width {
				padding: $padding;
			}

			@if $has-border-width and $has-border-style {
				border-width: $border-width;
				border-style: $border-style;
			}

			@if $has-border-radius {
				border-radius: $border-radius;
			}

			@else {
				border-radius: 0;
			}
		}

		.forminator-input {

			@if $has-line-height and $has-padding {
				height: #{$line-height + ($padding * 2)};
			}

			@else {
				height: auto;
			}
		}

		@content;
	}
}