@mixin multiselect($multiselect-map: null, $multiselect-theme: unset) {

	.forminator-multiselect {
		margin: 0;
		padding: 0;
		border: 0;

		&, li, label {
			display: block;
			@extend %basic-reset;
		}

		&, li {
			list-style: none;
		}

		li {

			input {
				@extend %screen-reader-only;
			}

			label {
				cursor: pointer;
			}
		}
	}

	@if $multiselect-map != null {

		$has-padding: map-has-key($multiselect-map, padding);
		$has-border-width: map-has-key($multiselect-map, border-width);
		$has-border-style: map-has-key($multiselect-map, border-style);
		$has-border-radius: map-has-key($multiselect-map, border-radius);
		$has-line-height: map-has-key($multiselect-map, line-height);
		$has-transition: map-has-key($multiselect-map, transition);

		$padding: map-get($multiselect-map, padding);
		$border-width: map-get($multiselect-map, border-width);
		$border-style: map-get($multiselect-map, border-style);
		$border-radius: map-get($multiselect-map, border-radius);
		$line-height: map-get($multiselect-map, line-height);
		$transition: map-get($multiselect-map, transition);

		.forminator-multiselect {

			li label {

				@if $has-padding and $has-border-width {
					padding: $padding #{$padding - $border-width};
				}

				@if $has-padding and not $has-border-width {
					padding: $padding;
				}

				@if $has-line-height {
					line-height: $line-height;
				}

				@if $has-transition {
					transition: $transition;
				}
			}
		}

		@if $multiselect-theme != material {

			.forminator-multiselect {
				max-height: 120px;
				overflow-x: hidden;
				overflow-y: auto;

				@if $has-border-width and $has-border-style {
					border-width: $border-width;
					border-style: $border-style;
				}

				@if $has-border-radius {
					border-radius: $border-radius;
				}
			}
		}

		@else {

			.forminator-multiselect {

				li {
					margin: #{$gutter-sm / 2} 0;

					label {
						position: relative;

						@if $has-border-width and $has-border-style {
							border-width: $border-width;
							border-style: $border-style;
						}
	
						@if $has-border-radius {
							border-radius: $border-radius;
						}

						&:after {
							content: " ";
							pointer-events: none;
							position: absolute;

							@if $has-border-width {
								top: -#{$border-width};
								right: -#{$border-width};
								bottom: -#{$border-width};
								left: -#{$border-width};
							}

							@if $has-border-radius {
								border-radius: $border-radius;
							}

							@if $has-transition {
								transition: $transition;
							}
						}
					}

					&:first-child {
						margin-top: 0;
					}

					&:last-child {
						margin-bottom: 0;
					}
				}
			}

			&:not(.forminator-size--small) .forminator-multiselect {

				li {

					&:first-child {
						
						@include media(min-width, sm) {
							margin-top: 0;
						}
					}

					&:last-child {
						
						@include media(min-width, sm) {
							margin-bottom: 0;
						}
					}

					@include media(min-width, sm) {
						margin: #{$gutter / 2} 0;
					}
				}
			}
		}
	}
}