@mixin quiz-question() {

	.forminator-question {
		margin: $gutter-sm 0;
		padding: 0;
		border: 0;

		legend {
			margin: 0;
			line-height: 1.7em;
			letter-spacing: 0;
		}

		.forminator-question--result {
			padding-bottom: $gutter-sm;
			border-bottom: 1px solid rgba(0,0,0,0.12);

			span {
				display: block;
				line-height: 1.8em;
			}
		}

		.forminator-question--has-image {

			img {
				width: auto;
				max-width: 100%;
				height: auto;
				display: block;
				margin: 0;
				padding: 0;
				border: 0;
			}
		}

		@content;
	}

	&:not(.forminator-size--small) {

		.forminator-question {

			@include media(min-width, sm) {
				margin: $gutter 0;
			}

			.forminator-question--result {

				@include media(min-width, sm) {
					padding-bottom: $gutter;
				}
			}
		}

		&.forminator-design--grid {

			.forminator-question {
				
				legend,
				.forminator-question--result {

					@include media(min-width, sm) {
						display: block;
						flex: 0 0 100%;
						padding: 0 #{$gutter / 2};
						text-align: center;
					}
				}

				.forminator-question--result {

					span {

						@include media(min-width, sm) {
							display: inline-flex;
							padding: 0 #{$gutter / 2} #{$gutter / 2};
							border-bottom: 1px solid rgba(0,0,0,0.12);
						}
					}

					@include media(min-width, sm) {
						padding-bottom: 0;
						border-bottom: 0;
					}
				}

				@include media(min-width, sm) {
					display: flex;
					flex-wrap: wrap;
					justify-content: center;
					margin-right: -#{$gutter / 2};
					margin-left: -#{$gutter / 2};
				}
			}
		}
	}
}