@mixin quiz-result($result-map: null, $result-theme: null) {

	.forminator-quiz--result {
		margin: $gutter-sm 0;

		&:first-child {
			margin-top: 0;
		}

		&:last-child {
			margin-bottom: 0;
		}
	}

	.forminator-result {

		@if $result-theme == material {

			@if $result-map != null {
	
				$has-border-radius: map-has-key($result-map, border-radius);
				$has-box-shadow: map-has-key($result-map, box-shadow);
	
				$border-radius: map-get($result-map, border-radius);
				$box-shadow: map-get($result-map, box-shadow);
	
				@if $has-border-radius {
					border-radius: $border-radius;
					overflow: hidden;
				}
	
				@if $has-box-shadow {
					box-shadow: $box-shadow;
				}
			}

			hr {
				display: block;
				padding: 0;
				border: 0;

				@if $result-map != null {

					$has-padding: map-has-key($result-map, padding);
					$has-break: map-has-key($result-map, break);

					$break: map-get($result-map, break);
					$padding: map-get($result-map, padding);

					@if $has-break and ($break == true) {
						display: block;
						background-color: $material--card-border;

						@if $has-padding {
							margin: $padding 0;
						}

						@else {
							margin: #{$gutter-sm / 2} 0;
						}
					}

					@else {
						display: none;
					}
				}

				@else {
					display: none;
				}
			}
		}

		@else {
			display: flex;
			flex-direction: column-reverse;

			@if $result-map != null {
				
				$has-padding: map-has-key($result-map, padding);
				$has-border-width: map-has-key($result-map, border-width);
				$has-border-style: map-has-key($result-map, border-style);
				$has-border-radius: map-has-key($result-map, border-radius);

				$padding: map-get($result-map, padding);
				$border-width: map-get($result-map, border-width);
				$border-style: map-get($result-map, border-style);
				$border-radius: map-get($result-map, border-radius);

				@if $has-padding and $has-border-width {
					padding: #{$padding - $border-width};
				}

				@else if $has-padding and not $has-border-width {
					padding: $padding;
				}

				@if $has-border-width and $has-border-style {
					border-width: $border-width;
				}

				@else {
					border-width: 0;
				}

				@if $has-border-style {
					border-style: $border-style;
				}

				@if $has-border-radius {
					border-radius: $border-radius;
				}
			}
		}

		+ .forminator-social--share {
			margin: $gutter 0 0;

			@include media(min-width, sm) {
				margin: $gutter 0 0;
			}
		}
	}

	.forminator-result--image {
		height: auto;
		max-height: 150px;
		display: block;
		padding: 0;
		border: 0;

		@if $result-theme == material {
			width: 100%;
			margin: 0;
		}

		@else {
			width: auto;
			margin: 0 auto;
		}
	}

	.forminator-result--title {
		margin: 0;
		padding: 0;
		border: 0;
	}

	.forminator-result--title {
		line-height: 2.3em;

		+ .forminator-result--description {

			@if $result-map != null {

				$has-padding: map-has-key($result-map, padding);
				$padding: map-get($result-map, padding);

				@if $has-padding {
					margin-top: $padding;
				}

				@else {
					margin-top: #{$gutter-sm / 2};
				}
			}

			@else {
				margin-top: #{$gutter-sm / 2};
			}
		}
	}

	.forminator-result--description {

		&, p {
			line-height: 1.9em;
		}

		h1, h2, h3, h4, h5, h6, p, ol, ul {

			&:first-child {
				margin-top: 0;
			}

			&:last-child {
				margin-bottom: 0;
			}
		}
	}

	.forminator-result--retake {
		margin: 0;

		@if $result-theme == material {
			display: inline-flex;
			padding: 0;
			border: 0;
			background-color: transparent;
			background-image: none;
			text-transform: uppercase;

			@if $result-map != null {

				$has-button-line-height: map-has-key($result-map, button-line-height);
				$button-line-height: map-get($result-map, button-line-height);

				@if $has-button-line-height {
					height: $button-line-height;
					line-height: $button-line-height;
				}

				@else {
					height: auto;
					line-height: 1.4em;
				}
			}

			@else {
				height: auto;
				line-height: 1.4em;
			}
		}

		@else {
			width: 100%;
			display: block;

			@if $result-map != null {

				$has-button-padding: map-has-key($result-map, button-padding);
				$has-button-border-radius: map-has-key($result-map, button-border-radius);
				$has-button-line-height: map-has-key($result-map, button-line-height);
				$has-button-icon: map-has-key($result-map, button-icon);
				$has-transition: map-has-key($result-map, transition);

				$button-padding: map-get($result-map, button-padding);
				$button-border-radius: map-get($result-map, button-border-radius);
				$button-line-height: map-get($result-map, button-line-height);
				$button-icon: map-get($result-map, button-icon);
				$transition: map-get($result-map, transition);

				@if $has-button-padding {
					padding: $button-padding;
				}

				@if $has-button-border-radius {
					border-radius: $button-border-radius;
				}

				@else {
					border-radius: 0;
				}

				@if $has-button-line-height {
					line-height: $button-line-height;
				}

				@if $has-button-icon {

					.wpdui-icon {
						font-size: $button-icon;
					}
				}
			}

			@else {
				border-radius: 0;
				line-height: 1.4em;
			}
		}
	}

	@if $result-theme == material {

		.forminator-result--content {

			@if $result-map != null {

				$has-padding: map-has-key($result-map, padding);
				$padding: map-get($result-map, padding);

				@if $has-padding {
					padding: $padding;
				}

				@else {
					padding: #{$gutter-sm / 2};
				}
			}

			@else {
				padding: #{$gutter-sm / 2};
			}
		}
	}

	@else {

		.forminator-result--info {

			@if $result-map != null {
				
				@if has-map-key($result-map, padding) {
					margin-top: map-get($result-map, padding);
				}

				@else {
					margin-top: #{$gutter-sm / 2};
				}
			}

			@else {
				margin-top: #{$gutter-sm / 2};
			}
		}

		.forminator-result--quiz-name {
			display: none;
		}

		.forminator-result--text {

			@if $result-map != null {

				@if map-has-key($result-map, padding) {
					padding: map-get($result-map, padding);

					+ .forminator-result--image {
						margin-top: map-get($result-map, padding);
					}
				}

				@else {
					padding: #{$gutter-sm / 2};

					+ .forminator-result--image {
						align-self: flex-start;
						margin-top: #{$gutter-sm / 2};
					}
				}
			}

			@else {
				padding: #{$gutter-sm / 2};

				+ .forminator-result--image {
					margin-top: #{$gutter-sm / 2};
				}
			}

			* {
				word-break: break-word;
			}
		}
	}

	&:not(.forminator-size--small) {
		
		.forminator-quiz--result {

			&:first-child {

				@include media(min-width, sm) {
					margin-top: 0;
				}
			}

			&:last-child {

				@include media(min-width, sm) {
					margin-bottom: 0;
				}
			}

			@include media(min-width, sm) {
				margin: $gutter 0;
			}
		}

		.forminator-result {

			@include media(min-width, sm) {

				@if $result-theme == material {
	
					hr {
		
						@if $result-map != null {
		
							$has-padding: map-has-key($result-map, padding);
							$has-break: map-has-key($result-map, break);
		
							$break: map-get($result-map, break);
		
							@if $has-break and ($break == true) {
		
								@if not $has-padding {
									margin: #{$gutter / 2} 0;
								}
							}
						}
					}
				}
	
				@else {
					display: block;
				}
			}
		}

		.forminator-result--title {
	
			+ .forminator-result--description {

				@include media(min-width, sm) {

					@if ($result-map != null) and not map-has-key($result-map, padding) {
						margin-top: #{$gutter / 2};
					}
		
					@else if $result-map == null {
						margin-top: #{$gutter / 2};
					}
				}
			}
		}

		.forminator-result--retake {

			@include media(min-width, sm) {
				
				@if $result-theme != material {
					width: auto;
					flex: 0 0 auto;
				}
			}
		}

		@if $result-theme == material {

			.forminator-result--content {

				@include media(min-width, sm) {

					@if ($result-map != null) and not map-has-key($result-map, padding) {
						padding: #{$gutter / 2};
					}
		
					@else if $result-map == null {
						padding: #{$gutter / 2};
					}
				}
			}
		}

		@else {

			.forminator-result--info {

				@include media(min-width, sm) {
					display: flex;
					margin-top: 0;

					@if $result-map != null {
				
						@if has-map-key($result-map, padding) {
							margin-bottom: map-get($result-map, padding);
						}
		
						@else {
							margin-bottom: #{$gutter-sm / 2};
						}
					}
		
					@else {
						margin-bottom: #{$gutter-sm / 2};
					}
				}
			}

			.forminator-result--quiz-name {

				+ .forminator-result--retake {

					@include media(min-width, sm) {

						@if $result-map != null {

							@if map-has-key($result-map, padding) {
								margin-left: map-get($result-map, padding);
							}

							@else {
								margin-left: #{$gutter / 2};
							}
						}

						@else {
							margin-left: #{$gutter / 2};
						}
					}
				}

				@include media(min-width, sm) {
					display: block;
					flex: 1;
					align-self: center;
				}
			}

			.forminator-result--content {

				@include media(min-width, sm) {
					display: flex;
				}
			}

			.forminator-result--text {

				+ .forminator-result--image {

					@include media(min-width, sm) {
						max-width: 50%;
						margin-top: 0;

						@if $result-map != null {

							@if map-has-key($result-map, padding) {
								margin-left: map-get($result-map, padding);
							}

							@else {
								margin-left: #{$gutter / 2};
							}
						}

						@else {
							margin-left: #{$gutter / 2};
						}
					}
				}

				@include media(min-width, sm) {}
			}
		}

		&.forminator-design--grid {

			.forminator-quiz--result {
				
				@include media(min-width, sm) {
					text-align: center;
				}
			}

			.forminator-result {
				
				@include media(min-width, sm) {
					text-align: initial;
				}
			}
		}
	}
}