@mixin section-border() {
	
	.forminator-break .forminator-border {
		width: 100%;
		height: 0;
		display: block;
		margin-right: 0;
		margin-bottom: 0;
		margin-left: 0;
		padding: 0;
		border-style: none;

		@content;

		&.solid {
			border-style: solid;
		}

		&.dashed {
			border-style: dashed;
		}
	}
}