@mixin button($button-map: null, $button-theme: null) {

	a.forminator-button,
	button.forminator-button {
		width: 100%;
		cursor: pointer;
		display: block;
		margin: $gutter-sm 0;
		border: 0;
		text-align: center;
		text-transform: none;

		&,
		&:hover,
		&:active,
		&:visited,
		&:disabled {
			outline: none;
			box-shadow: none;
			text-decoration: none;
		}

		&,
		&:hover,
		&:active,
		&:visited {
			@include opacity(1);
		}

		&:disabled {
			@include opacity(0.25);
		}
	
		&:first-child {
			margin-top: 0;
		}
	
		&:last-child {
			margin-bottom: 0;
		}

		@if $button-theme == material {
			overflow: hidden;
			position: relative;
			
			.forminator-button--mask,
			.forminator-button--text {
				display: block;
			}

			.forminator-button--mask {
                width: 0;
                position: absolute;
                top: 50%;
                left: 50%;
                padding: 0;
                border-radius: 100%;
                background-color: rgba(0,0,0,0.12);
                transform: translate(-50%, -50%);
                transition: width 450ms cubic-bezier(0.23, 1, 0.32, 1), .2s ease-out;
			}

			&:active {
                box-shadow: 0 1px 4px 0 rgba(0,0,0,0.6);

                .forminator-button--mask {
                    width: 120%;
                    padding: 120% 0 0;
                }
            }
		}

		@if $button-map != null {

			$has-padding: map-has-key($button-map, padding);
			$has-border-radius: map-has-key($button-map, border-radius);
			$has-line-height: map-has-key($button-map, line-height);
			$has-transition: map-has-key($button-map, transition);

			$padding: map-get($button-map, padding);
			$border-radius: map-get($button-map, border-radius);
			$line-height: map-get($button-map, line-height);
			$transition: map-get($button-map, transition);

			@if $has-padding {
				padding: $padding;
			}

			@if $has-border-radius {
				border-radius: $border-radius;
			}

			@else {
				border-radius: 0;
			}

			@if $has-line-height {
				line-height: $line-height;
			}

			@if $has-transition {
				transition: $transition;
			}
		}
	}

	&:not(.forminator-size--small) {

		a.forminator-button,
		button.forminator-button {

			&:first-child {

				@include media(min-width, sm) {
					margin-left: 0;
				}
			}
		
			&:last-child {
		
				@include media(min-width, sm) {
					margin-right: 0;
				}
			}
		
			@include media(min-width, sm) {
				width: auto;
				min-width: 100px;
				display: inline-block;
				margin: 0 #{$gutter / 2};
			}
		}
	}
}