@mixin wysiwyg($wysiwyg-map: null) {

	.wp-editor-wrap {

		.wp-switch-editor {
			height: auto;
			margin-top: 0;
			padding-top: 4px;
			font-size: 12px;
			line-height: 20px;
			font-weight: normal;
		}

		@content;
	}
}