// =============================================
// 1. LABELS

@include body-class(custom-form, material) {
	
	.forminator-label,
	.forminator-label--limit,
	.forminator-label--helper {
		line-height: $material--line-height;
	}

	.forminator-label--validation {
		margin-top: #{$material--padding / 2};
		padding: #{$material--padding / 2} $material--padding;
		line-height: $material--line-height;
	}
}

// =============================================
// 2. SECTION BORDER

@include body-class(custom-form, material) {

	@include section-border();
}

// =============================================
// 3. INPUT & TEXTAREA

@include body-class(custom-form, material) {

	@include input($material--input-map, material) {

		.forminator-input--wrap {
			position: relative;

			&:after {
                content: " ";
                height: 2px;
                display: block;
                position: absolute;
                right: 50%;
                bottom: 0;
                left: 50%;
				transition: $material--transition-alt;
            }
		}

		.forminator-textarea--wrap {
			position: relative;

			&:after {
				content: " ";
				pointer-events: none;
				position: absolute;
				z-index: 1;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				border-radius: $material--input-border-radius;
				transition: $material--transition;
			}
		}

		.forminator-floating--date,
		.forminator-floating--input,
		.forminator-floating--textarea {
			position: relative;
			z-index: 1;

			.forminator-label {
				overflow: hidden;
				pointer-events: none;
				transform-origin: 0;
				transition: transform $material--transition-alt, $material--transition;
				text-overflow: ellipsis;
				white-space: nowrap;
			}
		}

		.forminator-floating--date,
		.forminator-floating--input {
			
			.forminator-label {
				transform: translateY(#{$material--input-padding + $material--input-line-height});
			}
		}

		.forminator-floating--date {
			padding-left: #{$material--date-icon-size + ($gutter-sm / 2)};
		}

		.forminator-floating--textarea {
			height: 0;

			.forminator-label {
				padding-right: #{$material--input-padding * 2};
				transform: translate($material--input-padding, #{$material--input-padding + $material--input-line-height});
			}

			~ .forminator-textarea--wrap {

				.forminator-textarea {
					padding-top: #{($material--input-padding - $material--input-border-width) + $material--input-line-height};
				}
			}
		}

		.forminator-floating--date,
		.forminator-floating--input,
		.forminator-floating--textarea {

			+ .forminator-input,
			+ .forminator-date .forminator-input,
			+ .forminator-input--wrap .forminator-input,
			~ .forminator-textarea--wrap .forminator-textarea,
			+ .forminator-phone-intl .forminator-input--wrap .forminator-input {

				&:-moz-placeholder {
                    @include opacity(0);
                }
                
                &::-moz-placeholder {
                    @include opacity(0);
                }
				
                &:-ms-input-placeholder {
                    @include opacity(0);
                }
				
                &::-webkit-input-placeholder {
                    @include opacity(0);
                }
			}
		}

		.forminator-is_hover,
		.forminator-is_active,
		.forminator-has_error {

			.forminator-input--wrap {

				&:after {
					right: 0;
					left: 0;
				}
			}
		}

		.forminator-is_active,
		.forminator-is_filled {

			.forminator-floating--date,
			.forminator-floating--input {
				
				.forminator-label {
					transform: translateY(0);
                }
			}

			.forminator-floating--textarea {
    
				.forminator-label {
					transform: translate($material--input-padding, #{($material--input-padding / 2) - $material--input-border-width});
				}
			}
		}

		.forminator-is_active {

			.forminator-floating--date,
			.forminator-floating--input,
			.forminator-floating--textarea {

				+ .forminator-input,
				+ .forminator-date .forminator-input,
				+ .forminator-input--wrap .forminator-input,
				~ .forminator-textarea--wrap .forminator-textarea,
				+ .forminator-phone-intl .forminator-input--wrap .forminator-input {

					&:-moz-placeholder {
						@include opacity(1);
					}
					
					&::-moz-placeholder {
						@include opacity(1);
					}
					
					&:-ms-input-placeholder {
						@include opacity(1);
					}
					
					&::-webkit-input-placeholder {
						@include opacity(1);
					}
				}
			}
		}

		// Input with international validation
		.forminator-phone-intl {
			display: flex;
			align-items: center;

			.flag-container {
				cursor: pointer;
				flex: 0 0 auto;
				margin: 0 #{$gutter-sm / 2} 0 0;
				position: relative;
				padding: 0;

				.selected-flag {
					width: auto;
					height: auto;
					display: flex;
					align-items: center;
					padding: 0;

					.iti-flag,
					.iti-arrow {
						flex: 0 0 auto;
						position: unset;
					}

					.iti-flag {
						margin-right: 5px;
					}

					.iti-arrow {
						margin-top: 0;
					}
				}
			}

			.forminator-input--wrap {
				flex: 1;

				.forminator-input {
					padding-right: 0;
					padding-left: 0;
				}
			}
		}

		.intl-tel-input .forminator-floating--input {
			padding-left: #{31px + ($gutter-sm / 2)};
		}
	}

	&:not(.forminator-size--small) {

		.forminator-floating--date {
			padding-left: #{$material--date-icon-size + ($gutter / 2)};
		}
	}
}

// =============================================
// 4. RADIO & CHECKBOX

@include body-class(custom-form, material) {

	@include options(radio, $material--option-map, material);

	@include options(checkbox, $material--option-map, material);
}

// =============================================
// 5. SELECT

@include body-class(custom-form, material) {

	@include select($material--select-map, material);
}

// =============================================
// 6. SELECT DROPDOWN

@include dropdown(material, $material--dropdown-map);

// =============================================
// 7. MULTI SELECT

@include body-class(custom-form, material) {

	@include multiselect($material--multiselect-map, material);
}

// =============================================
// 8. DATE FIELD

@include body-class(custom-form, material) {

	@include date($material--date-map, material);
}

// =============================================
// 9. CALENDAR

@include calendar(material, $material--calendar-map);

// =============================================
// 10. BUTTON

@include body-class(custom-form, material) {

	@include button($material--button-map, material);
}

// =============================================
// 11. PAGINATION

@include body-class(custom-form, material) {

	@include pagination($material--pagination-map, material);
}