@mixin poll-actions($actions-map: null) {

	.forminator-poll--actions {
		margin: $gutter-sm 0 0;
		text-align: center;

		a:not(.forminator-button) {
			margin: 0;

			@if $actions-map != null {

				$has-font-size: map-has-key($actions-map, font-size);
				$has-line-height: map-has-key($actions-map, line-height);
				$has-font-weight: map-has-key($actions-map, font-weight);
		
				$font-size: map-get($actions-map, font-size);
				$line-height: map-get($actions-map, line-height);
				$font-weight: map-get($actions-map, font-weight);
		
				@if $has-font-size {
					font-size: $font-size;
				}
	
				@if $has-line-height {
					line-height: $line-height;
				}
	
				@if $has-font-weight {
					font-weight: $font-weight;
				}
			}

			&,
			&:hover,
			&:active,
			&:visited {
				outline: none;
				box-shadow: none;
				text-decoration: none;
			}
		}

		@if $actions-map != null {

			$has-padding: map-has-key($actions-map, padding);
			$has-border-radius: map-has-key($actions-map, border-radius);
			$has-font-size: map-has-key($actions-map, font-size);
			$has-line-height: map-has-key($actions-map, line-height);
			$has-font-weight: map-has-key($actions-map, font-weight);
	
			$padding: map-get($actions-map, padding);
			$border-radius: map-get($actions-map, border-radius);
			$font-size: map-get($actions-map, font-size);
			$line-height: map-get($actions-map, line-height);
			$font-weight: map-get($actions-map, font-weight);

			a.forminator-button,
			button.forminator-button {

				@if $has-padding {
					padding: $padding;
				}
				
				@if $has-border-radius {
					border-radius: $border-radius;
				}

				@else {
					border-radius: 0;
				}

				@if $has-font-size {
					font-size: $font-size;
				}

				@if $has-line-height {
					line-height: $line-height;
				}

				@if $has-font-weight {
					font-weight: $font-weight;
				}
			}
		}
	}

	&:not(.forminator-size--small) {

		.forminator-poll--actions {

			a:not(.forminator-button) {

				@include media(min-width, sm) {
					flex: 0 0 auto;
				}
			}

			@include media(min-width, sm) {
				display: flex;
				flex-wrap: wrap;
				align-items: center;
				margin: $gutter 0 0;
			}
		}
	}
}