// =============================================
// KEYFRAMES MENU
//
// 1. UPDATING
// 2. LOADING WHEEL
// 3. FADE IN UP
// 4. FADE OUT DOWN
// =============================================

// =============================================
// 1. UPDATING

@keyframes updating {
    from {
		transform: rotate(0deg);
	}
    to {
		transform: rotate(360deg);
	}
}

// =============================================
// 2. LOADING WHEEL
//
// This keyframe is based on Alan Shortis pen:
// https://codepen.io/alanshortis/pen/eJLVXr

@keyframes spin {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}

// =============================================
// 3. FADE IN UP

@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translate3d(0, 100%, 0);
    }
    to {
        opacity: 1;
        transform: none;
    }
}

// =============================================
// 4. FADE OUT DOWN

@keyframes fadeOutDown {
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
        transform: translate3d(0, 100%, 0);
    }
}

// =============================================
// 5. CLICK ANIMATION
//
// Used for Material UI radio and checkbox options.

@keyframes click-animation {
	0% {
		transform: scale(1);
		@include opacity(0.2);
	}
	50% {
		transform: scale(2);
		@include opacity(0.1);
	}
	100% {
		transform: scale(0);
		@include opacity(0);
	}
}