$material--select-line-height:        $material--line-height             !default;

$material--dropdown-padding:          $material--padding                 !default;
$material--dropdown-border-width:     $material--border-width            !default;
$material--dropdown-border-style:     $material--border-style            !default;
$material--dropdown-line-height:      $material--line-height             !default;

$material--multiselect-padding:       $material--padding                 !default;
$material--multiselect-border-width:  $material--border-width            !default;
$material--multiselect-border-style:  $material--border-style            !default;
$material--multiselect-border-radius: $material--border-radius           !default;
$material--multiselect-line-height:   $material--line-height             !default;

$material--date-icon:                 16px                               !default;
$material--date-icon-size:            16px                               !default;
$material--date-padding:              $material--padding                 !default;

$material--calendar-icon:             26px                               !default;
$material--calendar-icon-size:        16px                               !default;
$material--calendar-cell-size:        50px                               !default;
$material--calendar-padding:          5px                                !default;
$material--calendar-padding-head:     20px                               !default;
$material--calendar-shadow:           0 6px 10px $material--shadow-color !default;

$material--button-padding:            $material--padding                 !default;
$material--button-border-radius:      $material--border-radius           !default;
$material--button-line-height:        $material--line-height             !default;

$material--pagination-bar-size:       8px                                !default;
$material--pagination-dot-size:       21px                               !default;
$material--pagination-border-width:   $material--border-width            !default;
$material--pagination-border-style:   $material--border-style            !default;
$material--pagination-line-height:    $material--line-height             !default;

$material--question-font-size:        15px                               !default;
$material--question-line-height:      20px                               !default;
$material--question-font-weight:      bold                               !default;

$material--description-font-size:     13px                               !default;
$material--description-line-height:   18px                               !default;
$material--description-font-weight:   normal                             !default;

$material--actions-font-size:         14px                               !default;
$material--actions-line-height:       $material--line-height             !default;
$material--actions-font-weight:       normal                             !default;

$material--card-shadow:               0 1px 1px $material--shadow-color  !default;
$material--card-shadow-hover:         0 3px 8px $material--shadow-color  !default;

$material--answer-padding:            $material--padding                 !default;
$material--answer-border-width:       2px                                !default;
$material--answer-border-style:       $material--border-style            !default;
$material--answer-border-radius:      $material--border-radius           !default;
$material--answer-box-shadow:         $material--shadow-color            !default;

$material--result-padding:            $material--padding                 !default;
$material--result-border-radius:      $material--border-radius           !default;
$material--result-box-shadow:         0 1px 1px $material--shadow-color  !default;

$material--transition:                0.25s ease                         !default;
$material--transition-alt:            0.25s linear                       !default;

$material--input-map: (
	padding:       $material--input-padding,
	border-width:  $material--input-border-width,
	border-style:  $material--input-border-style,
	border-radius: $material--input-border-radius,
	line-height:   $material--input-line-height,
	transition:    $material--transition,
);

$material--option-map: (
	size:          $material--option-size,
	icon-size:     $material--option-icon,
	border-width:  $material--option-border-width,
	border-style:  $material--option-border-style,
	border-radius: $material--option-border-radius,
	line-height:   $material--option-line-height,
	transition:    $material--transition,
);

$material--select-map: (
	icon:          $material--select-icon,
	icon-size:     $material--select-icon-size,
	padding:       $material--select-padding,
	border-width:  $material--select-border-width,
	border-style:  $material--select-border-style,
	line-height:   $material--select-line-height,
	transition:    $material--transition,
);

$material--dropdown-map: (
	padding:       $material--dropdown-padding,
	line-height:   $material--dropdown-line-height,
	transition:    $material--transition,
);

$material--multiselect-map: (
	padding:       $material--multiselect-padding,
	border-width:  $material--multiselect-border-width,
	border-style:  $material--multiselect-border-style,
	border-radius: $material--multiselect-border-radius,
	line-height:   $material--multiselect-line-height,
	transition:    $material--transition,
);

$material--date-map: (
	icon-size:   $material--date-icon,
	icon-height: $material--date-icon-size,
	padding:     $material--date-padding,
);

$material--calendar-map: (
	icon:         $material--calendar-icon,
	icon-size:    $material--calendar-icon-size,
	cell-size:    $material--calendar-cell-size,
	padding:      $material--calendar-padding,
	padding-head: $material--calendar-padding-head,
	shadow:       $material--calendar-shadow,
	transition:   $material--transition,
);

$material--button-map: (
	padding:       $material--button-padding,
	border-radius: $material--button-border-radius,
	line-height:   $material--button-line-height,
	transition:    $material--transition,
