$material--poll-question: (
	font-size:   $material--question-font-size,
	line-height: $material--question-line-height,
	font-weight: $material--question-font-weight,
);

$material--poll-description: (
	font-size:   $material--description-font-size,
	line-height: $material--description-line-height,
	font-weight: $material--description-font-weight,
);

$material--poll-actions: (
	padding:     $material--button-padding,
	font-size:   $material--actions-font-size,
	line-height: $material--actions-line-height,
	font-weight: $material--actions-font-weight,
);

$material--quiz-answer: (
	padding:       $material--answer-padding,
	border-width:  $material--answer-border-width,
	border-style:  $material--answer-border-style,
	border-radius: $material--answer-border-radius,
	box-shadow:    $material--answer-box-shadow,
	transition:    $material--transition,
