@mixin notice($notice: default, $notice-padding: $notice--padding, $notice-radius: $border-radius) {
	height: auto;
	cursor: default;
	display: block;
	position: relative;
	margin: $gutter-sm 0;
	padding: $notice-padding;
	border-radius: $notice-radius;
	transition: height 0.3s linear, opacity 0.3s ease;

	&:before {
		width: $notice--icon-size;
		height: $notice--icon-size;
		display: block;
		position: absolute;
		top: #{$notice-padding + ( ($notice--line-height - $notice--icon-size) / 2)};
		left: $notice-padding;
		font-size: $notice--icon-size;
		text-align: center;
	}

	@if ($notice == info) {
		background-color: $notice--info-background;
		color: $notice--info-color;

		@include icon(before, $notice--icon-info, true) {
			color: $notice--info-color;
		}
	}

	@else if ($notice == error) {
		background-color: $notice--error-background;
		color: $notice--error-color;

		@include icon(before, $notice--icon-error, true) {
			color: $notice--error-color;
		}
	}

	@else if ($notice == default) {
		background-color: $notice--default-background;
		color: $notice--default-color;

		@include icon(before, $notice--icon-default, true) {
			color: $notice--default-color;
		}
	}
	
	@else if ($notice == success) {
		background-color: $notice--success-background;
		color: $notice--success-color;

		@include icon(before, $notice--icon-success, true) {
			color: $notice--success-color;
		}
	}

	@else if ($notice == loading) {
		background-color: $notice--loading-background;
		color: $notice--loading-color;

		&:before {
			width: 18px;
			height: 18px;
			border: 2px solid $notice--loading-icon-color;
			border-bottom-color: transparent;
			border-radius: 100%;
			box-sizing: border-box;
			animation: spin .75s 0s linear infinite;
		}
	}

	@else {

		@warn "Unfortunately, no type could be retrieved from `#{$notice}`. "
		+ "Use either `info`, `error`, `default`, `success`, or `loading`.";
	}

	* {
		color: inherit;
	}

	> span,
	> div {
		cursor: initial;
		display: block;
		padding-left: #{$notice--icon-size + $notice-padding};
	}

	&, p {
		font-size: 13px;
		line-height: $notice--line-height;
		font-family: $font;
		font-weight: 400;
		letter-spacing: $letter-spacing;

		strong {
			font-weight: bold;
		}
	}

	p {
		@extend %basic-reset;
	}

	&:first-child {
		margin-top: 0;
	}

	&:last-child {
		margin-bottom: 0;
	}

	@include media(min-width, sm) {
		margin: $gutter 0;
	}

	@content;
}